import React from "react";
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Card,
  CardTitle,
  CardText,
  Row,
  Col,
  Button,
} from "reactstrap";
import Order from "./Order";
import axios from "axios";
import moment from "moment";
import { useLocation } from "react-router-dom";
export default function Orders() {
  const [activeTab, setActiveTab] = React.useState("1");
  const [order, setorder] = React.useState({});
  const [orders, setorders] = React.useState([]);
  const [toggleorder, settoggleorder] = React.useState(false);
  const [previous, setprevious] = React.useState("");
  const token = localStorage.getItem("user-token");
  const location = useLocation();
  const toggle = (tab) => {
    if (activeTab !== tab) setActiveTab(tab);
  };

  React.useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    if (queryParams.get("order_id")) {
      var id = queryParams.get("order_id");
      setActiveTab("2");
      var temp_order = orders.find((item) => Number(item.id) === Number(id));
    if(temp_order){
        setorder(temp_order);
       settoggleorder(true);
    }

    }

    return () => {};
  }, [location.search, orders]);

  const [scheduled, setscheduled] = React.useState([]);
  const [completed, setcompleted] = React.useState([]);

  function classnames(data) {
    if (data.active) {
      return "active order_nav_item";
    }
    return "order_nav_item";
  }
  function openOrder(data) {
    setorder(data);
    settoggleorder(true);
  }
  function closeOrder(val) {
    setorder({});
    settoggleorder(false);
  }
  React.useEffect(() => {
    if (activeTab === "1") {
      setprevious("Completed");
    }
    if (activeTab === "2") {
      setprevious("Pending");
    }
    return () => {
      setorder({});
      setprevious("");
      settoggleorder(false);
    };
  }, [activeTab]);
  React.useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}/api/user/orders`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.status === 200) {
          setorders(res.data);
        }
      });

    return function cleanup() {
      setorders([]);
    };
  }, [token]);

  React.useEffect(() => {
    let scheduled = orders.filter(
      (item) => item.status === "pending" || item.status === "assigned"
    );
    let completed = orders.filter((item) => item.status === "delivered");

    setscheduled(scheduled);

    setcompleted(completed);

    return () => {};
  }, [orders]);

  return (
    <div>
      {toggleorder ? (
        <Order
          previous={previous}
          title="Months order"
          order={order}
          closeOrder={closeOrder}
        />
      ) : (
        <Row>
          <Col className="col-sm-12 orders-tab">
            <Nav tabs className="d-flex">
              <NavItem className={classnames({ active: activeTab === "1" })}>
                <NavLink
                  className="order_nav"
                  onClick={() => {
                    toggle("1");
                  }}
                >
                  Completed
                </NavLink>
              </NavItem>
              <NavItem className={classnames({ active: activeTab === "2" })}>
                <NavLink
                  className="order_nav"
                  onClick={() => {
                    toggle("2");
                  }}
                >
                  Pending
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId="1">
                <Row>
                  <Col sm="12">
                    <div style={{ height: 60 + "vh", overflowY: "auto" }}>
                      {completed.length ? (
                        <div>
                          {completed.map((item, index) => (
                            <Card body className="mb-3 order-card" key={index}>
                              <CardTitle className="d-flex justify-content-between mb-1">
                                <span className="text-warning font-weight-bold">
                                  {" "}
                                  {item.name}
                                </span>{" "}
                                <span className="text text-muted">
                                  {moment(item.created_at).format("DD/MM/YYYY")}
                                </span>
                              </CardTitle>
                              <CardText className="d-flex justify-content-between mb-1">
                                <span>Order Id : {item.order_no}</span>
                                <span className="text-muted">
                                  {" "}
                                  {item.items} items
                                </span>
                              </CardText>
                              <div className="d-flex justify-content-between align-items-center">
                                {item.status === "pending" &&
                                !item.isScheduled ? (
                                  <span className=" assigned  text-warning px-3 py-1">
                                    Assigned to agent
                                  </span>
                                ) : (
                                  ""
                                )}
                                {item.status === "pending" &&
                                item.isScheduled ? (
                                  <span className="bg-light scheduled px-3 py-1 rounded">
                                    Scheduled for delivery on{" "}
                                    {moment(order.scheduled_time).format(
                                      "DD/MM/YYYY"
                                    )}
                                  </span>
                                ) : (
                                  ""
                                )}
                                {item.status === "delivered" ? (
                                  <span className="delivered text-success px-3 py-1">
                                    Delivered{" "}
                                  </span>
                                ) : (
                                  ""
                                )}

                                <Button
                                  className="text-primary btn-link text"
                                  onClick={() => openOrder(item)}
                                >
                                  View details
                                </Button>
                              </div>
                            </Card>
                          ))}
                        </div>
                      ) : (
                        <div className="text-center">No order available</div>
                      )}
                    </div>
                  </Col>
                </Row>
              </TabPane>
              <TabPane tabId="2">
                <Row>
                  <Col>
                    <div style={{ height: 60 + "vh", overflowY: "auto" }}>
                      {scheduled.length ? (
                        scheduled.map((item, index) => (
                          <Card body className="mb-3 order-card" key={index}>
                            <CardTitle className="d-flex justify-content-between mb-1">
                              <span className="text-warning font-weight-bold">
                                {" "}
                                {item.name}
                              </span>{" "}
                              <span className="text text-muted">
                                {moment(item.created_at).format("DD/MM/YYYY")}
                              </span>
                            </CardTitle>
                            <CardText className="d-flex justify-content-between mb-1">
                              <span>Order Id : {item.order_no}</span>
                              <span className="text-muted">
                                {" "}
                                {item.items} items
                              </span>
                            </CardText>
                            <div className="d-flex justify-content-between align-items-center">
                              <span
                                size="sm"
                                className="text-muted bg-light px-3 py-1 "
                              >
                                Pending
                              </span>
                              <span
                                className="text-primary text cursor-pointer
                                "
                                onClick={() => openOrder(item)}
                              >
                                View details
                              </span>
                            </div>
                          </Card>
                        ))
                      ) : (
                        <div className="  text-center">
                          No order scheduled
                        </div>
                      )}
                    </div>
                  </Col>
                </Row>
              </TabPane>
            </TabContent>
          </Col>
        </Row>
      )}
    </div>
  );
}
