export const LogOutUserService = () => {
  const http = new HttpService();
  let loginUrl = "users/logout";
  const tokenId = "user-token";
  return http
    .getData(loginUrl, tokenId)
    .then((data) => {
      return data;
    })
    .catch((error) => {
      return error;
    });
};
