/*eslint-disable*/
import React from "react";
import moment from "moment";
// reactstrap components
import {
  Col,
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  Row,
  Label,
} from "reactstrap";
import defaultImage from "assets/img/default-avatar.png";

// import defaultImage from "assets/img/delivery-man.png";
import { useSelector } from "react-redux";
import states from "js/helpers/state.json";
import axios from "axios";
import { toast } from "react-toastify";
import { LoadProfileAction } from "../../../js/actions/profileaction";
import { useDispatch } from "react-redux";

// core components
const years = () => {
  const years = [];
  const dateStart = moment().subtract(100, "y");
  const dateEnd = moment();
  while (dateEnd.diff(dateStart, "years") >= 0) {
    years.push(dateStart.format("YYYY"));
    dateStart.add(1, "year");
  }
  return years.reverse();
};

const months = moment.months();
const days = [];

for (let index = 1; index <= 31; index++) {
  days.push(index);
}
const token = localStorage.getItem("user-token");

function AccountInfo() {
  const profile = useSelector((state) => state.userDetails.userProfile);
  const [cities, setcities] = React.useState([]);
  const [firstName, setfirstName] = React.useState("");
  const [surname, setSurname] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [gender, setGender] = React.useState("");
  const [phone, setPhone] = React.useState("");
  const [month, setMonth] = React.useState("");
  const [year, setYear] = React.useState("");
  const [date, setDate] = React.useState("");
  const [address, setAddress] = React.useState("");
  const [city, setCity] = React.useState("");
  const [state, setState] = React.useState("");
  const [profileImage, setprofileImage] = React.useState("");
  const [isuploading, setisuploading] = React.useState(false);
  const [isDisabled, setisDisabled] = React.useState(false)
  const dispatch = useDispatch();

  function handleFirstName(e) {
    setfirstName(e.target.value);
  }
  function handleSurname(e) {
    setSurname(e.target.value);
  }
  function handleEmail(e) {
    setEmail(e.target.value);
  }
  function handleAddress(e) {
    setAddress(e.target.value);
  }
  function handleMonth(e) {
    setMonth(e.target.value);
  }
  function handleYear(e) {
    setYear(e.target.value);
  }
  function handleDate(e) {
    setDate(e.target.value);
  }
  function handlePhone(e) {
    setPhone(e.target.value);
  }
  function handleGender(e) {
    setGender(e.target.value);
  }

  function handleState(e) {
    setState(e.target.value);
  }
  function handleCity(e) {
    setCity(e.target.value);
  }
  React.useEffect(() => {
    if (!state) {
      return;
    }
    let cities = states.find((item) => {
      return item.state.name === state;
    });
    if (cities) {
      setcities(cities.state.locals);
    }

    return () => {};
  }, [states, state]);

  React.useEffect(() => {
    setfirstName(profile.firstName);
    setSurname(profile.lastName);
    setAddress(profile.address);
    setEmail(profile.email);
    setGender(profile.gender ? profile.gender : "");
    setDate(profile.dob ? moment(profile.dob, "DD/MM/YYYY").format("DD") : "");
    setMonth(profile.dob ? moment(profile.dob, "DD/MM/YYYY").format("M") : "");
    setYear(
      profile.dob ? moment(profile.dob, "DD/MM/YYYY").format("YYYY") : ""
    );
    setState(profile.state ? profile.state : "");
    setCity(profile.city ? profile.city : "");
    setPhone(profile.phoneNumber);
    return () => {
      setfirstName("");
      setSurname("");
      setAddress("");
      setEmail("");
      setGender("");
      setDate("");
      setMonth("");
      setYear("");
      setState("");
      setCity("");
      setPhone("");
    };
  }, [profile]);

  function handleProfileImage(e) {
    setisuploading(true);
    const formData = new FormData();
    formData.append("file", e.target.files[0], e.target.files[0].name);
    formData.append("tags", "profileimage"); // Add tags for the images - {Array}
    formData.append(
      "upload_preset",
      process.env.REACT_APP_CLOUNDINARY_UPLOAD_PRESET
    ); // Replace the preset name with your own
    formData.append("api_key", process.env.REACT_APP_CLOUNDINARY_APIKEY); // Replace API key with your own Cloudinary API key
    formData.append("timestamp", (Date.now() / 1000) | 0);
    axios
      .post(`${process.env.REACT_APP_CLOUNDINARY_URL}`, formData, {
        headers: { "X-Requested-With": "XMLHttpRequest" },
      })
      .then((response) => {
        if (response.status === 200) {
          axios
            .post(
              `${process.env.REACT_APP_API_URL}/api/user/image`,
              { profileImage: response.data.secure_url },
              {
                headers: {
                  Authorization: `Bearer ${token}`,
                },
              }
            )
            .then((res) => {
              if (res.status === 200) {
                toast.success("Updated");
                dispatch(LoadProfileAction());
                setisuploading(false);
              }
            })
            .catch(() => {
              toast.error("Upload fail");
              setisuploading(false);
            });
        }
      })
      .catch(() => {
        toast.error("Upload fail");
        setisuploading(false);
      });
  }
  function handleUpdate(e) {
    e.preventDefault();
setisDisabled(true)
    let data = {
      firstName,
      surname,
      email,
      gender,
      phone,
      month,
      year,
      date,
      address,
      city,
      state,
    };

    axios
      .post(`${process.env.REACT_APP_API_URL}/api/user/profile/update`, data, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.status === 200) {
          toast.success("Updated");
          dispatch(LoadProfileAction());
          setisDisabled(false)
        }
      }).catch(()=>{
        setisDisabled(false)
        toast.error('Soemthing went wrong')
      });
  }
  return (
    <>
      <Row>
        <Col xl="8" lg="10" md="12">
          <Form className="" onSubmit={(e) => handleUpdate(e)}>
            <div className="profile-image-container ml-lg-0 ml-auto mr-auto d-flex justify-content-center align-items-center">
              <img
                src={profile.profileImage ? profile.profileImage : defaultImage}
                className="profile-image"
                alt={"profile"}
              />
              {!isuploading ? (
                ""
              ) : (
                <i
                  className="fa fa-spinner fa-spin fa-3x text-primary position-absolute"
                  aria-hidden="true"
                ></i>
              )}

              <div className="image-filter d-flex justify-content-center align-items-center">
                <FormGroup className="mb-0">
                  <Label for="file" className="cursor-pointer">
                    <Button type="button" className="btn-link mb-0">
                      <i className="fa fa-camera fa-2x" />
                    </Button>

                    <Input
                      type="file"
                      accept=".jpg, .jpeg, .png,.gif"
                      className="hidden"
                      name="file"
                      id="file"
                      onChange={(e) => handleProfileImage(e)}
                    />
                  </Label>
                </FormGroup>
              </div>
            </div>

            <div className="form-row">
              <FormGroup className="col-lg-6">
                <label>First Name</label>
                <Input
                  placeholder="First Name"
                  onChange={(e) => {
                    handleFirstName(e);
                  }}
                  defaultValue={firstName}
                  type="text"
                />
              </FormGroup>
              <FormGroup className="col-lg-6">
                <label>Surname</label>
                <Input
                  placeholder="Surname"
                  onChange={(e) => {
                    handleSurname(e);
                  }}
                  defaultValue={surname}
                  type="text"
                />
              </FormGroup>
            </div>
            <FormGroup>
              <label>Email Address</label>
              <Input
                placeholder="Email address"
                onChange={(e) => {
                  handleEmail(e);
                }}
                defaultValue={email}
                type="email"
              />
            </FormGroup>
            <div className="form-row">
              <FormGroup className="col-lg-6">
                <label>Gender</label>
                <Input
                  placeholder="City"
                  onChange={(e) => {
                    handleGender(e);
                  }}
                  value={gender}
                  type="select"
                >
                  <option value="">Choose...</option>
                  <option value="male">Male</option>
                  <option value="female">Female</option>
                </Input>
              </FormGroup>
              <FormGroup className="col-lg-6">
                <label> Mobile Number</label>
                <Input
                  placeholder="Address"
                  onChange={(e) => {
                    handlePhone(e);
                  }}
                  defaultValue={phone}
                  type="text"
                />
              </FormGroup>
            </div>
            <label>Date of Birth</label>
            <div className="form-row">
              <FormGroup className="col-lg-3">
                <Input
                  placeholder="Month"
                  onChange={(e) => {
                    handleMonth(e);
                  }}
                  value={month}
                  type="select"
                >
                  <option value="">Choose month</option>
                  {months.map((item, id) => (
                    <option value={id + 1} key={id}>
                      {item}
                    </option>
                  ))}
                </Input>
              </FormGroup>
              <FormGroup className="col-lg-3">
                <Input
                  placeholder="Day"
                  onChange={(e) => {
                    handleDate(e);
                  }}
                  value={Number(date) ? Number(date) : ""}
                  type="select"
                >
                  <option value="">Choose day</option>
                  {days.map((item, id) => (
                    <option value={item} key={id}>
                      {item}
                    </option>
                  ))}
                </Input>
              </FormGroup>
              <FormGroup className="col-lg-3">
                <Input
                  placeholder="Year"
                  onChange={(e) => {
                    handleYear(e);
                  }}
                  value={year}
                  type="select"
                >
                  <option value="">Choose year</option>
                  {years().map((item, id) => (
                    <option value={item} key={id}>
                      {item}
                    </option>
                  ))}
                </Input>
              </FormGroup>
            </div>
            {/* <FormGroup>
              <label>Address</label>
              <Input
                placeholder="Address"
                onChange={(e) => {
                  handleAddress(e);
                }}
                type="text"
                defaultValue={address}
              />
            </FormGroup> */}
            <div className="form-row">
              <FormGroup className="col-lg-6">
                <label>State</label>
                <Input
                  placeholder="State"
                  value={state}
                  onChange={(e) => {
                    handleState(e);
                  }}
                  type="select"
                >
                  <option value="">Choose...</option>
                  {states.map((item) => (
                    <option value={item.state.name} key={item.state.id}>
                      {item.state.name}
                    </option>
                  ))}
                </Input>
              </FormGroup>
              <FormGroup className="col-lg-6">
                <label>City</label>
                <Input
                  placeholder="City"
                  value={city}
                  onChange={(e) => {
                    handleCity(e);
                  }}
                  type="select"
                >
                  <option value="">Choose...</option>
                  {cities.map((item) => (
                    <option value={item.name} key={item.id}>
                      {item.name}
                    </option>
                  ))}
                </Input>
              </FormGroup>
            </div>
            <div className="form-row">
              <div className="col-lg-6">
                <Button
                  className="bg-primary-gradient"
                  type="submit"
                  color="primary"
                  block
                  disabled={isDisabled}
                >
                  Save Changes
                </Button>
              </div>
            </div>
          </Form>
        </Col>
      </Row>
    </>
  );
}

export default AccountInfo;
