import React, { useState, useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";
import { UpdatePasswordAction } from "../../js/actions/authaction";
import { useSelector, useDispatch } from "react-redux";
import { toast } from "react-toastify";

// reactstrap components
import {
  Button,
  Card,
  CardBody,
  CardFooter,
  Form,
  FormGroup,
  Input,
  FormText,
  Container,
  Col,
  Row,
} from "reactstrap";
import { Link } from "react-router-dom";

// core components
import DefaultNavbar from "../../components/Navbars/DefaultNavbar";
import CheckAnimation from "../../components/Animation/CheckAnimation";

function ResetPasswordPage() {
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [status, setStatus] = useState(false);
  const [token, setToken] = useState(null);
  const location = useLocation();
  const history = useHistory();
  const authResponse = useSelector((state) => state.userAuth.authResponse);
  const dispatch = useDispatch();
  const [isClicked, setisClicked] = useState(false);

  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    setToken(queryParams.get("token"));
    return () => {};
  }, [location.search, token]);

  useEffect(() => {
    document.body.classList.add("login-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("login-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);

  useEffect(() => {
    if (
      authResponse.success &&
      authResponse.message === "Your password has been changed"
    ) {
      setStatus(true);
      setisClicked(false);
    }
    if (!authResponse.success && authResponse.message === "Invalid request") {
      toast.error("Invalid Request");
      setisClicked(false);
    }
    return () => {};
  }, [authResponse]);

  function handlePassword(e) {
    setPassword(e);
  }
  function handleConfirmPassword(e) {
    setConfirmPassword(e);
  }

  function handleSubmit(e) {
    e.preventDefault();
    if (!password || !confirmPassword) {
      toast.error("Fill all fields");
      return;
    }
    var data = {
      token: token,
      password: password,
      confirmPassword: confirmPassword,
    };
    dispatch(UpdatePasswordAction(data, history));
  }
  return (
    <>
      <DefaultNavbar />
      <div className="page-header">
        <div className="content">
          <Container>
            {/*Forgot Password Block*/}
            {!status ? (
              <>
                <Row>
                  <Col className="ml-auto mr-auto mb-3 text-center" lg="7">
                    <h4 style={{ fontWeight: "600" }}>Reset your password</h4>
                    <p>
                      Please enter a new password and confirm it to have your
                      password changed, then you can login.
                    </p>
                  </Col>
                </Row>
                <Row className="justify-content-center align-items-center">
                  <Col className="ml-auto mr-auto" lg="4">
                    <Card className="card-login card-plain">
                      <Form onSubmit={(e) => handleSubmit(e)} className="form">
                        <CardBody className="pb-1">
                          <FormGroup>
                            <label htmlFor="password">New Password</label>
                            <Input
                              id="password"
                              placeholder="Password"
                              type="password"
                              value={password}
                              onChange={(e) => handlePassword(e.target.value)}
                            />
                            {password && password.length < 6 ? (
                              <FormText
                                className="text-muted"
                                color="danger"
                                id="emailHelp"
                              >
                                Should contain at least 6 characters
                              </FormText>
                            ) : (
                              ""
                            )}
                          </FormGroup>
                          <FormGroup>
                            <label htmlFor="confirmpassword">
                              Confirm Password
                            </label>
                            <Input
                              id="confirmpassword"
                              placeholder="Password"
                              type="password"
                              value={confirmPassword}
                              onChange={(e) =>
                                handleConfirmPassword(e.target.value)
                              }
                            />
                            {password &&
                            confirmPassword &&
                            password !== confirmPassword ? (
                              <FormText
                                className="text-muted"
                                color="danger"
                                id="emailHelp"
                              >
                                Password does not match
                              </FormText>
                            ) : (
                              ""
                            )}
                          </FormGroup>
                          <Button
                            className="bg-primary-gradient"
                            color="primary"
                            type="submit"
                            size="lg"
                            block
                            disabled={isClicked}
                          >
                            Reset Password
                          </Button>
                        </CardBody>
                        <CardFooter className="text-center">
                          <p>
                            Remembered your password?{" "}
                            <Link to="/login" style={{ fontWeight: "600" }}>
                              Login
                            </Link>
                          </p>
                        </CardFooter>
                      </Form>
                    </Card>
                  </Col>
                </Row>
              </>
            ) : (
              <div>
                {/*Email sent Block*/}
                <Row>
                  <Col className="ml-auto mr-auto text-center" lg="6">
                    <CheckAnimation />
                    <h4 style={{ fontWeight: "600" }}>
                      Your password has been changed successfully
                    </h4>
                  </Col>
                </Row>
                <Row className="justify-content-center align-items-center">
                  <Col className="ml-auto mr-auto" lg="4">
                    <Card className="card-login card-plain">
                      <CardBody className="">
                        <Button
                          className="bg-primary-gradient"
                          color="primary"
                          tag={Link}
                          to="/login"
                          size="lg"
                          block
                        >
                          Back to Login
                        </Button>
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
              </div>
            )}
          </Container>
        </div>
      </div>
    </>
  );
}

export default ResetPasswordPage;
