import React, { useEffect } from "react";
import { LogoutAction } from "../../js/actions/authaction";
// AddCartAction;
import { LoadCartAction, ResetError } from "../../js/actions/cartaction";
import { LoadProfileAction } from "../../js/actions/profileaction";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, Link } from "react-router-dom";
import { toast } from "react-toastify";
import axios from "axios";
import {
  UpdateCartAction,
  RemoveCartAction,
} from "../../js/actions/cartaction";
import currency from "../../js/methods/currency";
import location from "../../js/helpers/location";
// reactstrap components
import {
  Button,
  Collapse,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container,
} from "reactstrap";

import logo from "assets/img/brand/logo.png";
import Moment from "react-moment";
import {
  LoadNotificationAction,
  MarkNotificationAction,
} from "../../js/actions/notificationaction";

if (!localStorage.getItem("cartItems")) {
  localStorage.setItem("cartItems", JSON.stringify([]));
}

function DefaultNavbar({ updateCart, toggleCart }) {
  const [navbarColor, setNavbarColor] = React.useState("navbar-transparent");
  const [collapseOpen, setCollapseOpen] = React.useState(false);
  const [mycart, setmycart] = React.useState([false]);
  const cartItems = useSelector((state) => state.userCart.items);

  const history = useHistory();
  const dispatch = useDispatch();
  const authResponse = useSelector((state) => state.userAuth.authResponse);
  // const cart = useSelector((state) => state.userCart).items.length;

  const [dropdownOpen, setDropdownOpen] = React.useState(null);
  const notifications = useSelector(
    (state) => state.notifications.notifications
  );
  const error = useSelector((state) => state.userCart.error);

  useEffect(() => {
    location();
  }, []);

  useEffect(() => {
    if (error) {
      toast.error(error);
      dispatch(ResetError());
    }
  }, [error, dispatch]);

  // useEffect(() => {
  //   if (localStorage.getItem("cartItems")) {
  //     setmycart(JSON.parse(localStorage.getItem("cartItems")));
  //   }

  //   return function cleanup() {
  //     setmycart(JSON.parse(localStorage.getItem("cartItems")));
  //   };
  // }, [updateCart]);

  useEffect(() => {
    if (!localStorage.getItem("user-token")) {
      setmycart(JSON.parse(localStorage.getItem("cartItems")));
    } else {
      localStorage.setItem("cartItems", JSON.stringify(cartItems));
      setmycart(cartItems);
    }
  }, [cartItems]);
  const logOut = () => {
    dispatch(LogoutAction(history, toast));
  };
  const removecart = (id) => {
    if (!token) {
      removefromcart(id);
      toggleCart();
      return;
    }
    dispatch(RemoveCartAction(id));
  };

  function reducecart(id) {
    let cartArray = [];

    cartArray = JSON.parse(localStorage.getItem("cartItems"));

    cartArray.map((val) => {
      if (val.id === id) {
        val.quantity = val.quantity - 1;
      }
      return val;
    });

    localStorage.setItem("cartItems", JSON.stringify(cartArray));
    setmycart(cartArray);
    toast.success("Cart updated");
  }
  function addcart(id) {
    let cartArray = [];

    cartArray = JSON.parse(localStorage.getItem("cartItems"));

    cartArray.map((val) => {
      if (val.id === id) {
        val.quantity = val.quantity + 1;
      }
      return val;
    });

    localStorage.setItem("cartItems", JSON.stringify(cartArray));
    setmycart(cartArray);
    toast.success("Cart updated");
  }
  function removefromcart(id) {
    let cartArray = [];
    cartArray = JSON.parse(localStorage.getItem("cartItems"));
    var newArray = cartArray.filter((val) => val.id !== id);

    localStorage.setItem("cartItems", JSON.stringify(newArray));
    setmycart(newArray);
    toast.success("Cart updated");
  }

  const token = localStorage.getItem("user-token");

  const user = JSON.parse(localStorage.getItem("user-data"));
  const [unreadnotifications, setunreadnotifications] = React.useState(0);
  useEffect(() => {
    if (token) {
      dispatch(LoadCartAction());
      dispatch(LoadProfileAction());
    }
  }, [dispatch, token]);

  useEffect(() => {
    try {
      if (authResponse && authResponse === "logged out") {
        localStorage.removeItem("user-token");
        localStorage.removeItem("user-data");
        setDropdownOpen(false);
        history.push("/login");
      }
      return () => {};
    } catch (error) {}
  }, [authResponse, history, token]);

  useEffect(() => {
    const updateNavbarColor = () => {
      if (
        document.documentElement.scrollTop > 29 ||
        document.body.scrollTop > 29
      ) {
        setNavbarColor("");
      } else if (
        document.documentElement.scrollTop < 30 ||
        document.body.scrollTop < 30
      ) {
        setNavbarColor("navbar-transparent");
      }
    };
    window.addEventListener("scroll", updateNavbarColor);
    return function cleanup() {
      window.removeEventListener("scroll", updateNavbarColor);
    };
  });
  useEffect(() => {
    if (token) {
      dispatch(LoadNotificationAction());
    }
  }, [token, dispatch]);

  React.useEffect(() => {
    setunreadnotifications(
      notifications.filter((item) => !item.read_at).length
    );
  }, [notifications]);
  function handleAdd(id) {
    if (!token) {
      addcart(id);
      return;
    }
    var data = {
      action: "plus",
    };
    dispatch(UpdateCartAction(id, data));
  }
  function handleSubtract(id) {
    if (!token) {
      reducecart(id);
      return;
    }
    var data = {
      action: "subtract",
    };

    dispatch(UpdateCartAction(id, data));
  }
  function handleReadNotification(id) {
    dispatch(MarkNotificationAction(id));
  }
  function clearAll() {
    axios
      .delete(
        `${process.env.REACT_APP_API_URL}/api/user/notifications/delete`,
        { headers: { Authorization: `Bearer ${token}` } }
      )
      .then(() => {
        dispatch(LoadNotificationAction());
      });
  }
  return (
    <>
      {collapseOpen ? (
        <div
          id="bodyClick"
          onClick={() => {
            document.documentElement.classList.toggle("nav-open");
            setCollapseOpen(false);
          }}
        />
      ) : null}
      <Navbar className={"fixed-top " + navbarColor} expand="lg" color="white">
        <Container>
          <div className="navbar-translate">
            <span>
              <button
                className="navbar-toggler navbar-toggler mr-3"
                onClick={() => {
                  document.documentElement.classList.toggle("nav-open");
                  setCollapseOpen(!collapseOpen);
                }}
                aria-expanded={collapseOpen}
                type="button"
              >
                <span className="navbar-toggler-bar top-bar"></span>
                <span className="navbar-toggler-bar middle-bar"></span>
                <span className="navbar-toggler-bar bottom-bar"></span>
              </button>
              <NavbarBrand href="/" id="navbar-brand">
                <img src={logo} alt={"entermarket-logo"} />
              </NavbarBrand>
            </span>
            {/* <UncontrolledTooltip target="#navbar-brand">
              Home
            </UncontrolledTooltip> */}
            <Nav className="d-lg-none d-xl-none">
              {token ? (
                <>
                  <NavItem>
                    <NavLink
                      to="/profile"
                      tag={Link}
                      style={{
                        padding: "0rem 0.7rem",
                      }}
                    >
                      <img
                        src={user.profileImage}
                        width="20"
                        height="20"
                        alt="user"
                        style={{ objectFit: "cover" }}
                        className=" users_circle-08  rounded-circle mb-1"
                      />
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      to="/notifications"
                      tag={Link}
                      style={{
                        padding: "0rem 0.7rem",
                      }}
                    >
                      <span className="position-relative cursor-pointer">
                        {unreadnotifications ? (
                          <span className="badge badge-primary cart-no position-absolute">
                            {unreadnotifications}
                          </span>
                        ) : (
                          ""
                        )}

                        <i
                          className="far fa-bell"
                          style={{ fontSize: "20px" }}
                        />
                      </span>
                    </NavLink>
                  </NavItem>
                </>
              ) : (
                ""
              )}
              <NavItem className="d-flex ">
                <NavLink
                  to="/checkout"
                  tag={Link}
                  style={{
                    padding: "0rem 0.7rem",
                  }}
                >
                  <span className="position-relative cursor-pointer">
                    {mycart.length >= 1 ? (
                      <span className="badge badge-primary cart-no position-absolute">
                        {mycart.length}
                      </span>
                    ) : (
                      ""
                    )}

                    <i
                      className="now-ui-icons shopping_cart-simple"
                      style={{ fontSize: "20px" }}
                    />
                  </span>
                </NavLink>
              </NavItem>{" "}
            </Nav>
          </div>
          <Collapse
            className="justify-content-end"
            isOpen={collapseOpen}
            navbar
          >
            <Nav navbar>
              <NavItem>
                <NavLink to="/sign-up" tag={Link}>
                  <p className="font-weight-bold text-primary">
                    Become a partner
                  </p>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink to="/stores" tag={Link}>
                  <i className="fa fa-search mr-2 mr-md-0" />
                  <p className="d-lg-none d-xl-none ">Search</p>
                </NavLink>
              </NavItem>

              {token ? (
                <UncontrolledDropdown nav className="d-none d-md-flex">
                  <DropdownToggle
                    tag="span"
                    data-toggle="dropdown"
                    aria-expanded={dropdownOpen}
                    className="nav-link"
                    style={{
                      padding: "0.5rem 0.7rem",
                    }}
                  >
                    <span className="position-relative cursor-pointer">
                      {unreadnotifications ? (
                        <span className="badge badge-primary cart-no position-absolute">
                          {unreadnotifications}
                        </span>
                      ) : (
                        ""
                      )}

                      <i
                        className="far fa-bell mr-2 mr-md-0"
                        style={{ fontSize: "20px" }}
                      />
                      <p className="d-lg-none d-xl-none">Notifications</p>
                    </span>
                  </DropdownToggle>

                  <DropdownMenu right className="notifications-nav">
                    {notifications.length ? (
                      <div className="notification-container p-3">
                        <div className="d-flex justify-content-between align-items-center w-100 mb-3">
                          <strong>Notifications</strong>{" "}
                          <span
                            style={{ fontSize: "13px", cursor: "pointer" }}
                            onClick={() => clearAll()}
                          >
                            Clear all{" "}
                          </span>
                        </div>

                        {notifications.map((item) => (
                          <div
                            className="p-3 rounded notification-item mb-3"
                            key={item.id}
                          >
                            <div
                              className="mb-2"
                              onClick={() => handleReadNotification(item.id)}
                            >
                              <span
                                className={`${
                                  item.read_at ? "" : "font-weight-bold"
                                }`}
                              >
                                {item.data.message}
                              </span>
                            </div>
                            <div className="d-flex justify-content-between align-items-center detail">
                              <span>
                                {<Moment fromNow>{item.created_at}</Moment>}
                              </span>
                              {item.data.url ? (
                                <Button
                                  className="text-success btn-link m-0"
                                  href={item.data.url}
                                >
                                  View details
                                </Button>
                              ) : (
                                ""
                              )}
                            </div>
                          </div>
                        ))}
                      </div>
                    ) : (
                      <div
                        className="text-center p-3 rounded"
                        style={{ width: "300px" }}
                      >
                        No new notification
                      </div>
                    )}
                  </DropdownMenu>
                </UncontrolledDropdown>
              ) : (
                ""
              )}

              {!token ? (
                <NavItem>
                  <NavLink
                    to={"/login"}
                    className="d-none d-lg-block"
                    tag={Link}
                  >
                    <p>Login</p>
                  </NavLink>
                  <Button
                    className="nav-link d-lg-none mb-2"
                    color="light"
                    outline
                    to="/login"
                    tag={Link}
                  >
                    <p>Login</p>
                  </Button>
                </NavItem>
              ) : (
                ""
              )}

              {!token ? (
                <NavItem>
                  <Button
                    className="nav-link"
                    color="primary"
                    to="/sign-up"
                    tag={Link}
                  >
                    <p>Sign Up</p>
                  </Button>
                </NavItem>
              ) : (
                <UncontrolledDropdown nav className="d-none d-lg-flex">
                  <DropdownToggle
                    aria-expanded={false}
                    aria-haspopup={true}
                    caret
                    color="primary"
                    data-toggle="dropdown"
                    style={{ backgroundColor: "#F1F1F1", borderRadius: 5 }}
                    type="button"
                    nav
                    onClick={(e) => e.preventDefault()}
                  >
                    {/* <i className="now-ui-icons users_cir
                    cle-08 mr-1" /> */}
                    <img
                      src={user.profileImage}
                      width="20"
                      height="18"
                      alt="user"
                      style={{ objectFit: "cover" }}
                      className="users_circle-08 mr-1 rounded-circle"
                    />
                    <p>{token && user ? user.firstName : "Default"}</p>
                  </DropdownToggle>
                  <DropdownMenu>
                    <DropdownItem to="/profile" tag={Link}>
                      Profile
                    </DropdownItem>
                    {/* <DropdownItem to="/home" tag={Link}>
                      Messaging
                      <span className="bg-primary rounded text-white px-2 ml-4">
                        {" "}
                      </span>
                    </DropdownItem> */}
                    <DropdownItem to="/profile?showing=2" tag={Link}>
                      Wallet
                    </DropdownItem>
                    <DropdownItem to="/profile?showing=4" tag={Link}>
                      Orders
                    </DropdownItem>
                    <DropdownItem divider />
                    <DropdownItem onClick={logOut}>
                      <span className="text-danger">Logout</span>
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
              )}

              {token ? (
                <div className="d-md-none">
                  <NavItem>
                    <NavLink to="/profile" tag={Link}>
                      <i
                        className="fa fa-user  d-md-none mr-1"
                        aria-hidden="true"
                      ></i>{" "}
                      <p className="">Profile</p>
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink to="/profile?showing=2" tag={Link}>
                      <i
                        className="fa fa-lock  d-md-none mr-1"
                        aria-hidden="true"
                      ></i>{" "}
                      <p className="">Wallet</p>
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink to="/profile?showing=4" tag={Link}>
                      <i className="fas fa-boxes mr-1 d-md-none  "></i>{" "}
                      <p className="">Orders</p>
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink to="/profile?showing=3" tag={Link}>
                      <i className="fa fa-list mr-1" aria-hidden="true"></i>{" "}
                      <p className="">Shopping list</p>
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink to="/profile?showing=5" tag={Link}>
                      <i className="fas fa-shield-alt  mr-1 d-md-none  "></i>{" "}
                      <p className="">Security</p>
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink onClick={logOut}>
                      <i className="fas fa-sign-out-alt mr-1  d-md-none "></i>{" "}
                      <span className="text-danger">Logout</span>
                    </NavLink>
                  </NavItem>
                </div>
              ) : (
                ""
              )}

              <UncontrolledDropdown nav className="d-none d-lg-flex ">
                <DropdownToggle
                  tag="span"
                  data-toggle="dropdown"
                  aria-expanded={dropdownOpen}
                  className="nav-link"
                  style={{
                    padding: "0.5rem 0.7rem",
                  }}
                >
                  <span className="position-relative cursor-pointer">
                    {mycart.length >= 1 ? (
                      <span className="badge badge-primary cart-no position-absolute">
                        {mycart.length}
                      </span>
                    ) : (
                      ""
                    )}

                    <i
                      className="now-ui-icons shopping_cart-simple mr-2 mr-md-0"
                      style={{ fontSize: "20px" }}
                    />
                    <p className="d-lg-none d-xl-none">Cart</p>
                  </span>
                </DropdownToggle>

                <DropdownMenu
                  right
                  className="notification-cart p-3"
                  style={{ width: "400px" }}
                >
                  {mycart.length ? (
                    <div key={mycart.id}>
                      <div className="cart-block ">
                        <div className="d-flex justify-content-between align-items-center w-100 mb-2 ">
                          <strong>Cart</strong>
                        </div>
                        {mycart.map((item, index) => (
                          <div className="cart-item-block w-100" key={index}>
                            <img
                              alt="..."
                              className="product-img"
                              src={item.image}
                            />
                            <div style={{ flex: 1 }}>
                              <div className="d-flex justify-content-between">
                                <div>
                                  <p className="product-name d-block">
                                    {item.product_name}
                                  </p>
                                  <p className="product-location d-block">
                                    <i className="fa fa-map-marker-alt mr-2" />
                                    {item.store_name}
                                  </p>
                                </div>
                                <Button
                                  onClick={() => removecart(item.id)}
                                  className="btn-icon btn-link mt-0"
                                  color="danger"
                                  type="button"
                                  size="sm"
                                >
                                  <i className="fa fa-trash-alt" />
                                </Button>
                              </div>

                              <div className="d-flex align-items-center justify-content-between mt-2">
                                <p className="product-price h5">
                                  {currency(item.price)}
                                </p>
                                <div className="action-buttons d-flex align-items-center justify-content-between">
                                  <Button
                                    onClick={() => handleSubtract(item.id)}
                                    className="btn-icon m-0 btn-round"
                                    color=""
                                    type="button"
                                    size="sm"
                                    disabled={item.quantity === 1}
                                  >
                                    <i className="now-ui-icons ui-1_simple-delete" />
                                  </Button>
                                  <span className="product-amount">
                                    {item.quantity}
                                  </span>
                                  <Button
                                    onClick={() => handleAdd(item.id)}
                                    className="btn-icon m-0 btn-round"
                                    color=""
                                    type="button"
                                    size="sm"
                                  >
                                    <i className="now-ui-icons ui-1_simple-add" />
                                  </Button>
                                </div>
                              </div>
                            </div>
                          </div>
                        ))}
                      </div>
                      <div className="px-4">
                        <Button
                          color="primary"
                          className="bg-primary-gradient"
                          block
                          href="/checkout"
                        >
                          Proceed to checkout
                        </Button>
                      </div>
                    </div>
                  ) : (
                    <div className="text-center">No item in cart</div>
                  )}
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>
          </Collapse>
        </Container>
      </Navbar>
    </>
  );
}

export default DefaultNavbar;
