import * as ActionTypes from "../constants/action-types";
const initState = {
  authResponse: "",
  token: localStorage.getItem("user-token"),
  error:null
};
const AuthReducer = (state = initState, action) => {
  switch (action.type) {
    case ActionTypes.RESTART_AUTH_RESPONSE:
      return {
        ...state,
        authResponse: "",
        error: null,
      };
    case ActionTypes.LOADING:
      return {
        ...state,
        authResponse: "loading...",
      };
    case ActionTypes.SIGNUP_SUCCESS:
      return {
        ...state,
        authResponse: action.res,
      };
    case ActionTypes.SIGNUP_ERROR:
      return {
        ...state,
        authResponse: action.res,
      };
    case ActionTypes.LOGIN_SUCCESS:
      return {
        ...state,
        authResponse: "logged in",
      };
    case ActionTypes.LOGIN_ERROR:
      return {
        ...state,
        error: action.res,
        authResponse: action.res,
      };
    case ActionTypes.LOGOUT_SUCCESS:
      return {
        ...state,
        authResponse: "logged out",
      };
    case ActionTypes.LOGOUT_ERROR:
      return {
        ...state,
        authResponse: action.res,
      };
    case ActionTypes.SEND_PASSWORD_RESET_SUCCESS:
      return {
        ...state,
        authResponse: action.res,
      };

    case ActionTypes.SEND_PASSWORD_RESET_ERROR:
      return {
        ...state,
        authResponse: action.res,
      };
    case ActionTypes.UPDATE_PASSWORD_SUCCESS:
      return {
        ...state,
        authResponse: action.res,
      };

    case ActionTypes.UPDATE_PASSWORD_ERROR:
      return {
        ...state,
        authResponse: action.res,
      };

    case ActionTypes.CODE_ERROR:
      return {
        ...state,
        authResponse:
          "There seems to be a problem, please refresh your browser",
      };
    default:
      return state;
  }
};
export default AuthReducer;
