/*eslint-disable*/
import React from "react";

// reactstrap components
import { Button, Input, FormGroup } from "reactstrap";
import axios from "axios";
import currency from "../../../js/methods/currency";
import { toast } from "react-toastify";
// core components

function Electricity({ confirmInfo , plainTab}) {
  const [meternumber, setmeternumber] = React.useState("");
  const [amount, setamount] = React.useState("");
  const [servicetype, setservicetype] = React.useState("");
  const [company, setcompany] = React.useState("");
  const [services, setservices] = React.useState([]);
  const payviame_token = localStorage.getItem("payviame-token");
  const [billers, setbillers] = React.useState([]);
  const [companydetail, setcompanydetail] = React.useState("");
  const [detail, setdetail] = React.useState("");
  const [user, setuser] = React.useState("");
  const [isverifying, setisverifying] = React.useState(false);
  const [isclickable, setisclickable] = React.useState(false);


   React.useEffect(() => {
setservicetype("")
      setamount("");
      setmeternumber("");

      setdetail("");
      setcompany("");
      setcompanydetail("");
      setuser("");


    }, [plainTab]);
  React.useEffect(() => {
    axios
      .get("https://apis.payviame.com/api/electric-billers", {
        headers: {
          Authorization: `Bearer ${payviame_token}`,
        },
      })
      .then((res) => {
        if (res.status === 200) {
          var data = res.data.data.sort((a, b) =>
            a.billername > b.billername
              ? 1
              : b.billername > a.billername
              ? -1
              : 0
          );
          setbillers(data);
        }
      });
    return () => {};
  }, [payviame_token]);

  React.useEffect(() => {
    if (!company) {
      return;
    }
    setcompanydetail(
      billers.find((item) => parseInt(item.billerid) === parseInt(company)).billername
    );
    axios
      .get(`https://apis.payviame.com/api/electric-items/${company}`, {
        headers: {
          Authorization: `Bearer ${payviame_token}`,
        },
      })
      .then((res) => {
        if (res.status === 201) {
          setservices(res.data.data[0]);
        }
      });

    return () => {};
  }, [company, payviame_token]);
  React.useEffect(() => {
    if (!servicetype) return;
    var result = services.find((item) => item.paymentCode === servicetype);
    if (!result) return;
    setamount(result.amount / 100);
    setdetail(result.paymentitemname);
    return () => {};
  }, [servicetype]);

  function handleNumber(value) {
    setmeternumber(value);
  }
  function handleServiceType(value) {
    setservicetype(value);
  }
  function handleCompany(value) {
    setservicetype("");
    setcompany(value);
  }
  function handlePurchase(type) {
    if (!company) {
      toast.error("Choose a company");
      return;
    }

    if (!servicetype) {
      toast.error("Choose a service");
      return;
    }
    if (!meternumber || meternumber.length === 11) {
      toast.error("Provide a valid meter number");
      return;
    }

    var data = {
      company,
      amount,
      meternumber,
      servicetype,
      companydetail,
      detail,
      type,
      customerid: meternumber,
      paymentCode: servicetype,
    };
    confirmInfo(data);
  }
  function verify() {
    if (!meternumber || !servicetype || meternumber.length < 7) {
      toast.error("Fill all fields");
      return;
    }
    setisverifying(true);
    var data = {
      customerid: meternumber,
      paymentCode: servicetype,
    };

    axios
      .post("https://apis.payviame.com/api/validate-customer", data, {
        headers: {
          Authorization: `Bearer ${payviame_token}`,
        },
      })
      .then((res) => {
        if (res.data.status === "success") {
          setisclickable(true);
          setuser(res.data.data.fullName);
          setisverifying(false);
        } else {
        }
      })
      .catch((err) => {
        setisverifying(false);
        toast.error(err.response.data.message);
      });
  }

  return (
    <>
      <div className="airtime-data">
        <FormGroup>
          <label>Distribution Company</label>
          <Input
            placeholder="Select Company"
            value={company}
            onChange={(e) => handleCompany(e.target.value)}
            type="select"
          >
            <option disabled value="">
              Select Company
            </option>
            {billers.length ? (
              billers.map((item) => (
                <option key={item.billerid} value={item.billerid}>
                  {item.billername}
                </option>
              ))
            ) : (
              <option disabled value="">
                Loading Company
              </option>
            )}
          </Input>
        </FormGroup>
        <FormGroup>
          <label>Service Type</label>
          <Input
            placeholder="Select Type"
            value={servicetype}
            onChange={(e) => handleServiceType(e.target.value)}
            type="select"
          >
            <option disabled value="">
              Select Service
            </option>
            {services.length ? (
              services.map((item, index) => (
                <option key={index} value={item.paymentCode}>
                  {item.paymentitemname}
                </option>
              ))
            ) : (
              <option disabled value="">
                Loading Services
              </option>
            )}
          </Input>
        </FormGroup>
        <FormGroup>
          <label>Amount (Naira)</label>
          <Input
            placeholder="Amount"
            value={currency(amount)}
            type="number"
            readOnly
          />
        </FormGroup>
        <FormGroup>
          <label>Meter Number</label>
          <Input
            onChange={(e) => handleNumber(e.target.value)}
            placeholder="Meter Number"
            value={meternumber}
            type="number"
          />
        </FormGroup>{" "}
        {isclickable ? (
          ""
        ) : (
          <Button
            className="bg-light-gradient mt-5 text-black"
            color="light"
            size="lg"
            onClick={() => verify()}
            block
            disabled={isverifying ? true : false}
          >
            Verify Detail{" "}
            {isverifying ? (
              <i className="fa fa-spinner fa-spin" aria-hidden="true"></i>
            ) : (
              ""
            )}
          </Button>
        )}
        {isclickable ? (
          <Button
            className="bg-primary-gradient mt-5"
            color="primary"
            size="lg"
            onClick={() => handlePurchase("cable")}
            block
            disabled={isclickable ? false : true}
          >
            Pay for Electric
          </Button>
        ) : (
          ""
        )}
      </div>
    </>
  );
}

export default Electricity;
