import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import {  Redirect } from "react-router-dom";
// reactstrap components
import {
  Button,
  Card,
  CardBody,
  CardFooter,
  Form,
  FormGroup,
  Input,
  InputGroup,
  FormText,
  Container,
  Col,
  Row,
  Label,
} from "reactstrap";

import authImage from "assets/img/illustrations/auth-img.png";
// core components
import DefaultNavbar from "../../components/Navbars/DefaultNavbar";
import { LoginAction, RestartAuthResponse } from "../../js/actions/authaction";
import { useDispatch } from "react-redux";
import { useHistory, Link ,useLocation} from "react-router-dom";

 const  LoginPage =() =>{
   const [passwordView , setpasswordView] = useState(false)
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [passwordValid, setPasswordValid] = useState(true);
  const [emailValid, setEmailValid] = useState(true);
  const dispatch = useDispatch();
  const history = useHistory();
  const token = localStorage.getItem("user-token");
  const authResponse = useSelector((state) => state.userAuth.authResponse);
  const error = useSelector((state) => state.userAuth.error);
  const [isClicked, setisClicked] = useState(false);
const location = useLocation();
  // useEffect(() => {
  //   if (token) {
  //     history.push("/home");
  //   }
  //   return () => {};
  // }, [history, token]);

  useEffect(() => {
    document.body.classList.add("login-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("login-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);

  useEffect(() => {

    if (
      !authResponse.success &&
      authResponse.message &&
      authResponse.message.password
    ) {
      setPasswordValid(false);
      setTimeout(() => {
        setPasswordValid(true);
      }, 2000);
    }
    if (
      !authResponse.success &&
      authResponse.message &&
      authResponse.message.email
    ) {
      setEmailValid(false);
      setTimeout(() => {
        setEmailValid(true);
      }, 2000);
    }
    if (
      error &&
      !error.success
    ) {
      toast.error(error.message);
      setisClicked(false);
      setEmailValid(false);
      setPasswordValid(false);
      setTimeout(() => {
        setEmailValid(true);
        setPasswordValid(true);
      }, 3000);
      dispatch(RestartAuthResponse());
    }
    // if (error) {
    //   toast.error(error.message);
    //   setisClicked(false);
    //    dispatch(RestartAuthResponse());
    // }

    return () => {
      dispatch(RestartAuthResponse());
    };
  }, [authResponse, dispatch, error]);
  function handleEmail(e) {
    setEmail(e);
  }
  function handlePassword(e) {
    setPassword(e);
  }
  function handleSubmit(e) {
    setisClicked(true);
    e.preventDefault();
    let data = {
      email: email,
      password: password,
    };

    dispatch(LoginAction(data, history, location));
  }
  function handleSocialLogin(provider) {
    var url = `${process.env.REACT_APP_API_URL}/api/auth/${provider}/redirect`;

    window.open(
      url,
      "Social_Login",
      "toolbar=no,scrollbars=no,location=no,statusbar=no,menubar=no,resizable=0,width=100,height=100,left = 490,top = 262"
    );
  }
  function togglePassword(){
    setpasswordView(!passwordView)
  }

 if(!token){
    return (
      <>
        <DefaultNavbar />
        <div className="page-header">
          <div className="content">
            <Container>
              <Row>
                <Col
                  className="ml-auto mr-auto mt-4 mt-md-0 mb-3 text-center"
                  md="8"
                >
                  <h4 className="text-primary" style={{ fontWeight: "600" }}>
                    Login to EnterMarket
                  </h4>
                </Col>
              </Row>
              <Row className="justify-content-center align-items-center">
                <Col lg="6" className="d-none d-md-block">
                  <img alt="..." className="img-fluid" src={authImage} />
                </Col>
                <Col className=" mt-4 mt-md-0" lg="4">
                  <Card className="card-login card-plain">
                    <Form onSubmit={handleSubmit} className="form">
                      <CardBody>
                        <FormGroup>
                          <label htmlFor="exampleInputEmail1">
                            Email address
                          </label>
                          <Input
                            aria-describedby="emailHelp"
                            id="exampleInputEmail1"
                            placeholder="Enter email"
                            type="email"
                            onChange={(e) => handleEmail(e.target.value)}
                            value={email}
                            className="shadow-sm"
                          />
                          {!emailValid ? (
                            <FormText
                              className="text-muted"
                              color="danger"
                              id="emailHelp"
                            >
                              Incorrect email. Try again!
                            </FormText>
                          ) : (
                            ""
                          )}
                        </FormGroup>
                        <FormGroup>
                          <label htmlFor="exampleInputPassword1">
                            Password
                          </label>

                          <InputGroup className="shadow-sm border">
                            <Input
                              id="exampleInputPassword1"
                              placeholder="Password"
                              type={passwordView ? "text" : "password"}
                              onChange={(e) => handlePassword(e.target.value)}
                              value={password}
                              className="shadow-none"
                            />

                            <Button
                              onClick={() => togglePassword()}
                              className="w-auto m-0 bg-white text-dark border border-left-0 p-2"
                            >
                              {" "}
                              <i
                                className={
                                  !passwordView
                                    ? "fa fa-eye-slash px-1"
                                    : "fa fa-eye px-1"
                                }
                                aria-hidden="true"
                              ></i>
                            </Button>
                          </InputGroup>
                        </FormGroup>
                        <div className="d-flex justify-content-between align-items-center">
                          {!passwordValid ? (
                            <FormText
                              className="text-muted"
                              color="danger"
                              id="emailHelp"
                            >
                              Incorrect password. Try again!
                            </FormText>
                          ) : (
                            ""
                          )}
                          <Link
                            className="form-text text-dark"
                            to="/forgot-password"
                          >
                            Forgot Password
                          </Link>
                        </div>
                        <div className="d-flex justify-content-between align-items-center my-4">
                          <FormGroup check className="mt-0">
                            <Label check>
                              <Input type="checkbox" />
                              <span className="form-check-sign" />
                              Remember me
                            </Label>
                          </FormGroup>
                          <Button
                            className="bg-primary-gradient px-5"
                            color="primary"
                            type="submit"
                            size="lg"
                            disabled={isClicked}
                          >
                            Login{" "}
                            {isClicked ? (
                              <i
                                className="fa fa-spinner fa-spin"
                                aria-hidden="true"
                              ></i>
                            ) : (
                              ""
                            )}
                          </Button>
                        </div>
                        <div className="text-center">
                          <p className="form-text mb-0">
                            Or login with your registered social pages
                          </p>
                        </div>
                      </CardBody>
                      <CardFooter className="text-center btn-social">
                        <Button
                          block
                          className="google-btn"
                          color="danger"
                          type="button"
                          onClick={() => handleSocialLogin("google")}
                          size="lg"
                        >
                          <i className="fab fa-google-plus-g" />
                          Login with Gmail
                        </Button>
                        {/* <Button
                          block
                          className="facebook-btn"
                          color="info"
                          href="#pablo"
                          onClick={(e) => e.preventDefault()}
                          size="lg"
                        >
                          <i className="fab fa-facebook-f" />
                          Login with Facebook
                        </Button> */}
                        <div className="text-center mt-5">
                          <p>
                            Not registered yet?{" "}
                            <Link to="/sign-up" style={{ fontWeight: "600" }}>
                              Sign up
                            </Link>
                          </p>
                        </div>
                      </CardFooter>
                    </Form>
                  </Card>
                </Col>
              </Row>
            </Container>
          </div>
        </div>
      </>
    );
 }else{
 return (
   <Redirect
     to={{
       pathname: "/home",

     }}
   />
 );
}
}

export default LoginPage;