import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import {
  SendPasswordResetAction,
  RestartAuthResponse,
} from "../../js/actions/authaction";
import { toast } from "react-toastify";

// reactstrap components
import {
  Button,
  Card,
  CardBody,
  CardFooter,
  Form,
  FormGroup,
  Input,
  FormText,
  Container,
  Col,
  Row,
} from "reactstrap";
import { Link } from "react-router-dom";

import inboxImage from "assets/img/illustrations/email-inbox.png";

// core components
import DefaultNavbar from "../../components/Navbars/DefaultNavbar";

function ForgotPasswordPage() {
  const [email, setEmail] = useState("");
  const [emailValid, setEmailValid] = useState("");
  const dispatch = useDispatch();
  const authResponse = useSelector((state) => state.userAuth.authResponse);
  const [isSent, setisSent] = useState(false);
  const history = useHistory();
  const [isClicked, setisClicked] = useState(false);

  useEffect(() => {
    document.body.classList.add("login-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("login-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);

  useEffect(() => {
    if (
      authResponse.success === false &&
      authResponse.message === "email error"
    ) {
      setisClicked(false);
      toast.error(authResponse.message);
      setEmailValid("error");
      setTimeout(() => {
        setEmailValid(true);
      }, 2500);
    }
    if (
      authResponse.success === true &&
      authResponse.message === "email sent"
    ) {
      setisClicked(false);
      toast.success("Password reset link sent, check your mail!");
      setEmailValid("success");
      setEmail("");
      setisSent(true);
    }
    return () => {
      dispatch(RestartAuthResponse());
    };
  }, [authResponse, dispatch]);

  function handleEmail(e) {
    setEmail(e);
  }

  function handleSubmit(e) {
    e.preventDefault();
    var data = {
      email: email,
    };
    dispatch(SendPasswordResetAction(data, history));
  }
  return (
    <>
      <DefaultNavbar />
      <div className="page-header">
        <div className="content">
          <Container>
            {/*Email sent Block*/}
            <>
              {isSent ? (
                <>
                  {" "}
                  <Row>
                    <Col className="ml-auto mr-auto text-center" lg="6">
                      <img
                        alt="..."
                        className="inbox-image mb-4"
                        src={inboxImage}
                      />
                      <p className="mb-0">
                        The reset link has been sent to your email address,
                        please make use of it within 24 hours.
                      </p>
                    </Col>
                  </Row>
                  <Row className="justify-content-center align-items-center">
                    <Col className="ml-auto mr-auto" lg="4">
                      <Card className="card-login card-plain">
                        <CardBody className="">
                          <Button
                            className="bg-primary-gradient"
                            color="primary"
                            tag={Link}
                            to="/login"
                            size="lg"
                            block
                          >
                            Back to Login
                          </Button>
                        </CardBody>
                      </Card>
                    </Col>
                  </Row>
                </>
              ) : (
                <>
                  <Row>
                    <Col
                      className="ml-auto mr-auto mb-3 mt-4 mt-md-0 text-center"
                      lg="7"
                    >
                      <h4 style={{ fontWeight: "600" }}>
                        Forgot your password?{" "}
                      </h4>
                      <p>
                        Not to worry, just enter your email address below and we
                        will send you reset instructions to get you back into
                        your account.
                      </p>
                    </Col>
                  </Row>
                  <Row className="justify-content-center align-items-center">
                    <Col className="ml-auto mr-auto" lg="4">
                      <Card className="card-login card-plain">
                        <Form
                          onSubmit={(e) => handleSubmit(e)}
                          className="form"
                        >
                          <CardBody className="pb-1">
                            <FormGroup>
                              <label htmlFor="exampleInputEmail1">
                                Email address
                              </label>
                              <Input
                                aria-describedby="emailHelp"
                                id="exampleInputEmail1"
                                placeholder="Enter email"
                                type="email"
                                required
                                onChange={(e) => handleEmail(e.target.value)}
                                value={email}
                              />
                              {emailValid === "success" ? (
                                <FormText
                                  className="text-muted"
                                  color="success"
                                  id="emailHelp"
                                >
                                  Password reset link sent, check your mail!
                                </FormText>
                              ) : (
                                ""
                              )}
                              {emailValid === "error" ? (
                                <FormText
                                  className="text-muted"
                                  color="danger"
                                  id="emailHelp"
                                >
                                  Email does not exist with us !
                                </FormText>
                              ) : (
                                ""
                              )}
                            </FormGroup>
                            <Button
                              className="bg-primary-gradient"
                              color="primary"
                              type="submit"
                              size="lg"
                              block
                              disabled={isClicked}
                            >
                              Send reset link
                            </Button>
                          </CardBody>
                          <CardFooter className="text-center">
                            <p>
                              Remembered your password?{" "}
                              <Link to="/login" style={{ fontWeight: "600" }}>
                                Login
                              </Link>
                            </p>
                          </CardFooter>
                        </Form>
                      </Card>
                    </Col>
                  </Row>
                </>
              )}
            </>
          </Container>
        </div>
      </div>
    </>
  );
}

export default ForgotPasswordPage;
