/*eslint-disable*/
import React from "react";

// reactstrap components
import { Container, Col, Button, Row } from "reactstrap";
import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemButton,
  AccordionItemPanel,
} from "react-accessible-accordion";
import "react-accessible-accordion/dist/fancy-example.css";

import defaultImage from "assets/img/default-avatar.png";
// import defaultImage from "assets/img/delivery-man.png";
import axios from "axios";
import currency from "../../../js/methods/currency";
import { Link } from "react-router-dom";
import { toast } from "react-toastify";
import { LoadCartAction } from "../../../js/actions/cartaction";
import { useDispatch } from "react-redux";
// core components

function ShoppingList() {
  const dispatch = useDispatch();
  const [lists, setlists] = React.useState([]);
  const token = localStorage.getItem("user-token");
  React.useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}/api/wishlists`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.status === 200) {
          setlists(res.data);
        }
      });

    return function cleanup() {
      setlists([]);
    };
  }, []);

  function addListToCart(id) {
    axios
      .get(`${process.env.REACT_APP_API_URL}/api/add-wishlist/${id}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.status === 200) {
          if (res.data === "created") {
            dispatch(LoadCartAction());
            toast.success("Added to cart");
            var newlist = lists.map((val) => {
              if (val.id == id) {
                val.wishlistitems = [];
              }
              return val;
            });
            setlists(newlist);
          }
        }
      });
  }

  return (
    <>
      <Container>
        <h6 className="text-left text-success text-capitalize mb-4">
          My Shopping List
        </h6>
        {lists.length ? (
          <Row>
            <Col sm="12" className="lists text-center">
              <Accordion allowZeroExpanded>
                {lists.map((item, index) => (
                  <AccordionItem key={item.id} className="list-item">
                    <AccordionItemHeading>
                      <AccordionItemButton>
                        <div className="list-title accordion-header d-flex justify-content-between align-items-center w-100">
                          <span className="font-weight-bold"> {item.name}</span>
                          <span>{item.wishlistitems.length} items</span>
                        </div>
                      </AccordionItemButton>
                    </AccordionItemHeading>
                    <AccordionItemPanel>
                      <div className="accordion-content">
                        {item.wishlistitems.map((val) => (
                          <div
                            key={val.id}
                            className="accordion-content-text d-flex justify-content-between"
                          >
                            <div className="mb-2 text-left ">
                              <div className="d-flex">
                                <div>
                                  <img
                                    alt="..."
                                    className="product-img mr-1"
                                    width="40"
                                    height="60"
                                    style={{ objectFit: "cover" }}
                                    src={val.product.image[0]}
                                  />
                                </div>
                                <div className="text-muted text-md">
                                  {val.product.product_name} <br />
                                  <p className="text-sm">
                                    {" "}
                                    {val.store.name} {val.store.location}
                                  </p>
                                </div>
                              </div>
                            </div>
                            <span>
                              {" "}
                              <span className="mr-2 text-md">
                                {currency(
                                  val.product.sales_price
                                    ? val.product.sales_price
                                    : val.product.price
                                )}
                              </span>
                              <Link to={`/product-details/${val.product.id}`}>
                                {" "}
                                <i
                                  className="fa fa-chevron-right"
                                  aria-hidden="true"
                                ></i>
                              </Link>
                            </span>
                          </div>
                        ))}
                        {item.wishlistitems.length ? (
                          <div className="text-right mt-3">
                            <Button
                              color="success"
                              size="sm"
                              onClick={() => addListToCart(item.id, index)}
                            >
                              Add to cart
                            </Button>
                          </div>
                        ) : (
                          <div className="text-center"> List empty</div>
                        )}
                      </div>
                    </AccordionItemPanel>
                  </AccordionItem>
                ))}
              </Accordion>
            </Col>
          </Row>
        ) : (
          <Row>
            <Col className="p-5 text-center">
              <h5 className="mb-1 text-muted">No list created yet</h5>
              <Link to="/stores">
                <Button color="success">
                  Search for products to add to the list
                </Button>
              </Link>
            </Col>
          </Row>
        )}
      </Container>
    </>
  );
}

export default ShoppingList;
