import axios from "axios";
const axiosApiInstance = axios.create();
const payviame_token = localStorage.getItem("payviame-token");
const token = localStorage.getItem("user-token");
// Request interceptor for API calls
axiosApiInstance.interceptors.request.use(
  async (config) => {
    config.headers = {
      Authorization: `Bearer ${payviame_token}`,
      Accept: "application/json",
    };
    return config;
  },
  (error) => {
    Promise.reject(error);
  }
);

// Response interceptor for API calls
axiosApiInstance.interceptors.response.use(
  (response) => {
    return response;
  },
  async function (error) {
    const originalRequest = error.config;

    if (
      error.response.data.message.toLowerCase() ===
      "token can not be used, get new one" ||  error.response.data.message.toLowerCase() ===
      "token is expired"
    ) {
      originalRequest._retry = true;
      const access_token = await refreshAccessToken();
      console.log(
        "🚀 ~ file: api_helper.js ~ line 29 ~ access_token",
        access_token.data
      );
      // axios.defaults.headers.common["Authorization"] = "Bearer " + access_token;
      return axiosApiInstance(originalRequest);
    }
    return Promise.reject(error);
  }
);
function refreshAccessToken() {
  return axios.get(
    `${process.env.REACT_APP_API_URL}/api/refresh/token/${payviame_token}`,
    {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    }
  );
}
export default axiosApiInstance;
