import React from "react";

// reactstrap components
import { Col } from "reactstrap";

import "react-accessible-accordion/dist/fancy-example.css";

// core components
import { Link } from "react-router-dom";
import currency from "../../js/methods/currency";

function SimilarProducts({ similarItems }) {
  return (
    <>
      <p className="font-weight-bold mt-4">
        Similar products people are searching
      </p>
      <div className="d-flex flex-row w-100 overflow-auto ">
        {similarItems.map((item) => (
          <Col className="product-listing" lg="3" md="4" key={item.id}>
            <Link to={`/product-details/${item.id}`} className="d-block">
              <div className="product-block">
                <p className="product-location">
                  <i className="fa fa-map-marker-alt mr-2" />
                  {item.store.name}
                </p>
                <img alt="..." className="product-img" src={item.image[0]} />
              </div>

              <p className="product-name">{item.product_name}</p>
              <div className="d-flex align-items-center justify-content-end">
                <p className="product-price h4">
                  {currency(item.sales_price ? item.sales_price : item.price)}
                </p>

              </div>
            </Link>
          </Col>
        ))}
      </div>
    </>
  );
}

export default SimilarProducts;
