/*eslint-disable*/
import React from "react";

// reactstrap components
import {
  Col,
  Button,
  NavItem,
  NavLink,
  Nav,
  TabContent,
  TabPane,
  Row,
  FormGroup,
  Input,
  Form,
  FormText,
} from "reactstrap";
import defaultImage from "../../../assets/img/default-avatar.png";
import { useState } from "react";
import axios from "axios";
import { toast } from "react-toastify";

// core components

function Security() {
  const [plainTabs, setPlainTabs] = React.useState("1");
  const [oldpassword, setoldpassword] = useState("");
  const [newpassword, setnewpassword] = useState("");
  const [confirmpassword, setconfirmpassword] = useState("");
  const [isvalid, setisvalid] = useState(null);
  const [isvalidPin, setisvalidPin] = useState(null);
  const [oldpin, setoldpin] = useState("");
  const [newpin, setnewpin] = useState("");
  const [confirmpin, setconfirmpin] = useState("");
  const token = localStorage.getItem("user-token");

  function handleOld(e) {
    setoldpassword(e.target.value);
  }
  function handleNew(e) {
    setnewpassword(e.target.value);
  }
  function handleConfirm(e) {
    setconfirmpassword(e.target.value);
  }

  function handleOldPin(e) {
    if (isNaN(Number(e.target.value))) {
      setoldpin("");
      return;
    }
    setoldpin(e.target.value);
  }
  function handleNewPin(e) {
     if (isNaN(Number(e.target.value))) {
       setnewpin("");
       return;
     }
    setnewpin(e.target.value);
  }
  function handleConfirmPin(e) {
     if (isNaN(Number(e.target.value))) {
       setconfirmpin("");
       return;
     }
    setconfirmpin(e.target.value);
  }

  function handleSubmit(e) {
    e.preventDefault();
    if (confirmpassword.length > 3 && newpassword === confirmpassword) {
      setisvalid(true);
    } else {
      setisvalid(false);
      setTimeout(() => {
        setisvalid(null);
      }, 3000);
      return;
    }
    let data = {
      oldpassword,
      newpassword,
      confirmpassword,
    };
    axios
      .post(`${process.env.REACT_APP_API_URL}/api/change/password`, data, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.data.success) {
          toast.success(res.data.message);
          setnewpassword("");
          setconfirmpassword("");
          setoldpassword("");
        }
        if (!res.data.success) {
          toast.error(res.data.message);
        }
      })
      .catch((err) => {
        toast.error(err.response.data.message);
      });
  }
  function handleSubmitPin(e) {
    e.preventDefault();
    if (confirmpin.length && newpin === confirmpin) {
      setisvalidPin(null);
    } else {
      setTimeout(() => {
        setisvalid(true);
      }, 3000);
      setisvalidPin(false);
      return;
    }
    let data = {
      newpin,
      confirmpin,
    };
    axios
      .post(`${process.env.REACT_APP_API_URL}/api/create/pin`, data, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.data.success) {
          toast.success("Pin created");
          setnewpin("");
          setoldpin("");
           setconfirmpin("");
        }
        if (!res.data.success) {
          toast.error(res.data.message);
        }
      })
      .catch((err) => {
        toast.error(err.response.data.message);
      });
  }

  function handleSubmitPinReset(e) {
    e.preventDefault();
    if (confirmpin.length && newpin === confirmpin) {
      setisvalidPin(true);
    } else {
      setTimeout(() => {
        setisvalid(null);
      }, 3000);
      setisvalidPin(false);
      return;
    }
    let data = {
      oldpin,
      newpin,
      confirmpin,
    };
    axios
      .post(`${process.env.REACT_APP_API_URL}/api/change/pin`, data, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (res.data.success) {
          toast.success("Pin changed");
          setnewpin("");
          setconfirmpin("");
          setoldpin("");
        }
        if (!res.data.success) {
          toast.error(res.data.message);
        }
      })
      .catch((err) => {
        toast.error(err.response.data.message);
      });
  }
  return (
    <>
      <Row className="">
        <Col xl="4" lg="6" md="12">
          <div className="security-tab">
            <Nav className="flex-column pt-0" role="tablist" tabs>
              <NavItem>
                <NavLink
                  className={plainTabs === "1" ? "active" : ""}
                  href="#pablo"
                  onClick={(e) => {
                    e.preventDefault();
                    setPlainTabs("1");
                  }}
                >
                  Change Password
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={plainTabs === "2" ? "active" : ""}
                  href="#pablo"
                  onClick={(e) => {
                    e.preventDefault();
                    setPlainTabs("2");
                  }}
                >
                  Create PIN
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={plainTabs === "3" ? "active" : ""}
                  href="#pablo"
                  onClick={(e) => {
                    e.preventDefault();
                    setPlainTabs("3");
                  }}
                >
                  Reset PIN
                </NavLink>
              </NavItem>
            </Nav>
          </div>
        </Col>
        <Col xl="8" lg="6" md="12">
          <TabContent activeTab={"plainTabs" + plainTabs}>
            <TabPane tabId="plainTabs1">
              <Row>
                <Col>
                  <Form className="" onSubmit={(e) => handleSubmit(e)}>
                    <div className="form-row">
                      <FormGroup className="col-lg-6">
                        <label>Old Password</label>
                        <Input
                          placeholder="Old Password"
                          onChange={(e) => handleOld(e)}
                          value={oldpassword}
                          type="password"
                        />
                      </FormGroup>
                    </div>
                    <div className="form-row">
                      <FormGroup className="col-lg-6">
                        <label>New Password</label>

                        <Input
                          onChange={(e) => handleNew(e)}
                          placeholder="New Password"
                          value={newpassword}
                          type="password"
                        />
                      </FormGroup>
                    </div>
                    <div className="form-row">
                      <FormGroup className="col-lg-6">
                        <label>Re-enter New Password</label>
                        <Input
                          onChange={(e) => handleConfirm(e)}
                          placeholder="Re-enter New Password"
                          value={confirmpassword}
                          type="password"
                        />
                        {isvalid === false ? (
                          <FormText
                            className="text-muted"
                            color="danger"
                            id="emailHelp"
                          >
                            Passwords don't match. Try again!
                          </FormText>
                        ) : (
                          ""
                        )}
                      </FormGroup>
                    </div>
                    <div className="form-row">
                      <div className="col-lg-6">
                        <Button
                          className="bg-primary-gradient"
                          color="primary"
                          type="submit"
                          block
                        >
                          Change Password
                        </Button>
                      </div>
                    </div>
                  </Form>
                </Col>
              </Row>
            </TabPane>
            <TabPane tabId="plainTabs2">
              <Row>
                <Col>
                  <Form className="" onSubmit={(e) => handleSubmitPin(e)}>
                    <div className="form-row">
                      <FormGroup className="col-lg-6">
                        <label>Create PIN</label>
                        <Input
                          placeholder="Create PIN"
                          onChange={(e) => handleNewPin(e)}
                          value={newpin}
                          type="password"
                          minLength="4"
                          maxLength="4"
                        />
                      </FormGroup>
                    </div>
                    <div className="form-row">
                      <FormGroup className="col-lg-6">
                        <label>Confirm PIN</label>
                        <Input
                          placeholder="Confirm PIN"
                          onChange={(e) => handleConfirmPin(e)}
                          value={confirmpin}
                          type="password"
                          minLength="4"
                          maxLength="4"
                        />
                        {isvalidPin === false ? (
                          <FormText
                            className="text-muted"
                            color="danger"
                            id="emailHelp"
                          >
                            Pin don't match. Try again!
                          </FormText>
                        ) : (
                          ""
                        )}
                      </FormGroup>
                    </div>
                    <div className="form-row">
                      <div className="col-lg-6">
                        <Button
                          className="bg-primary-gradient"
                          color="primary"
                          block
                          type="submit"
                        >
                          Create PIN
                        </Button>
                      </div>
                    </div>
                  </Form>
                </Col>
              </Row>
            </TabPane>
            <TabPane tabId="plainTabs3">
              <Row>
                <Col>
                  <Form className="" onSubmit={(e) => handleSubmitPinReset(e)}>
                    <div className="form-row">
                      <FormGroup className="col-lg-6">
                        <label>Old Pin</label>
                        <Input
                          placeholder="Old PIN"
                          onChange={(e) => handleOldPin(e)}
                          value={oldpin}
                          type="password"
                          minLength="4"
                          maxLength="4"
                        />
                      </FormGroup>
                    </div>
                    <div className="form-row">
                      <FormGroup className="col-lg-6">
                        <label>New Pin</label>
                        <Input
                          placeholder="New PIN"
                          onChange={(e) => handleNewPin(e)}
                          value={newpin}
                          type="password"
                          minLength="4"
                          maxLength="4"
                        />
                      </FormGroup>
                    </div>
                    <div className="form-row">
                      <FormGroup className="col-lg-6">
                        <label>Re-enter New Pin</label>
                        <Input
                          placeholder="Confirm PIN"
                          onChange={(e) => handleConfirmPin(e)}
                          value={confirmpin}
                          type="password"
                          minLength="4"
                          maxLength="4"
                        />
                        {isvalidPin === false ? (
                          <FormText
                            className="text-muted"
                            color="danger"
                            id="emailHelp"
                          >
                            Pin don't match. Try again!
                          </FormText>
                        ) : (
                          ""
                        )}
                      </FormGroup>
                    </div>
                    <div className="form-row">
                      <div className="col-lg-6">
                        <Button
                          className="bg-primary-gradient"
                          color="primary"
                          block
                          type="submit"
                        >
                          Change Pin
                        </Button>
                      </div>
                    </div>
                  </Form>
                </Col>
              </Row>
            </TabPane>
          </TabContent>
        </Col>
      </Row>
    </>
  );
}

export default Security;
