import React from "react";
import { Container, Row, Col, ListGroup, ListGroupItem } from "reactstrap";
import { useSelector } from "react-redux";
import Moment from "react-moment";
import { useHistory } from "react-router-dom";
import DefaultNavbar from "components/Navbars/DefaultNavbar.js";
import DefaultFooter from "components/Footers/DefaultFooter.js";
import { MarkNotificationAction } from "js/actions/notificationaction";
import { useDispatch } from "react-redux";

function Notification() {
  const token = localStorage.getItem("user-token");
  const history = useHistory();
  const dispatch = useDispatch();
  const notifications = useSelector(
    (state) => state.notifications.notifications
  );

  React.useEffect(() => {
    document.body.classList.add("landing-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("landing-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);
  React.useEffect(() => {
    if (!token) {
      history.push("/login");
    }
  }, [token, history]);

  function handleReadNotification(id) {
    dispatch(MarkNotificationAction(id));
  }
  return (
    <>
      <DefaultNavbar />
      <div className="wrapper">
        <section className="section notifications mt-5">
          <Container>
            <Row>
              <Col>
                {notifications.length ? (
                  <div className="notification-container p-3">
                    <div className="d-flex justify-content-between align-items-center w-100 mb-3">
                      <strong>Notifications</strong>
                    </div>

                    {notifications.length ? (
                      <ListGroup>
                        {notifications.map((item) => (
                          <ListGroupItem
                            className=" rounded notification-item mb-3"
                            key={item.id}
                            onClick={() => handleReadNotification(item.id)}
                          >
                            <div className="mb-2">
                              <span
                                className={`${
                                  item.read_at ? "" : "font-weight-bold"
                                }`}
                              >
                                {item.data.message}
                              </span>
                            </div>
                            <div className="d-flex justify-content-between detail">
                              <span></span>
                              <small>
                                {<Moment fromNow>{item.created_at}</Moment>}
                              </small>
                            </div>
                          </ListGroupItem>
                        ))}
                      </ListGroup>
                    ) : (
                      ""
                    )}
                  </div>
                ) : (
                  <div className="text-center">No new notification</div>
                )}
              </Col>
            </Row>
          </Container>
        </section>
      </div>
      <DefaultFooter />
    </>
  );
}

export default Notification;
