import React from "react";

// reactstrap components
import {
  Button,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
} from "reactstrap";

function ProductFilter({
  id,
  brandid,
  storecategories,
  handleCategories,
  category,
  brands,
  brand,
  handleBrands,
  handlesetsortvalue,
}) {
  const [filter, setfilter] = React.useState("category");

  function handleSort(val, e) {
    e.preventDefault();
    setfilter(val);
    handlesetsortvalue(val)
  }
  return (
    <>
      <div className="product-filter">
        <UncontrolledDropdown>
          <DropdownToggle
            size="sm"
            aria-expanded={false}
            aria-haspopup={true}
            caret
            color="warning"
            data-toggle="dropdown"
            id="dropdownMenuButton"
            type="button"
          >
            Filter by {filter}
          </DropdownToggle>
          <DropdownMenu aria-labelledby="dropdownMenuButton">
            <DropdownItem
              href="#pablo"
              onClick={(e) => handleSort("category", e)}
            >
              Category
            </DropdownItem>
            <DropdownItem href="#pablo" onClick={(e) => handleSort("brand", e)}>
              Brand
            </DropdownItem>
            {/* <DropdownItem href="#pablo" onClick={(e) => e.preventDefault()}>
              Stores
            </DropdownItem> */}
          </DropdownMenu>
        </UncontrolledDropdown>

        {filter === "category" ? (
          <>
            <UncontrolledDropdown className="d-md-none">
              <DropdownToggle
                size="sm"
                aria-expanded={false}
                aria-haspopup={true}
                caret
                color="warning"
                data-toggle="dropdown"
                id="dropdownMenuButton"
                type="button"
              >
                {category}
              </DropdownToggle>
              <DropdownMenu aria-labelledby="dropdownMenuButton">
                <DropdownItem
                  href="#pablo"
                  onClick={() => handleCategories("all", "All")}
                >
                  All
                </DropdownItem>
                {storecategories.map((item, idx) => (
                  <DropdownItem
                    key={idx}
                    href="#pablo"
                    onClick={() => handleCategories(item.id, item.name)}
                  >
                    {item.name}
                  </DropdownItem>
                ))}
              </DropdownMenu>
            </UncontrolledDropdown>
            <div className="product-buttons d-none d-md-block">
              <Button
                className={id === "all" ? "" : "btn-link"}
                color={id === "all" ? "warning" : ""}
                size="sm"
                onClick={() => handleCategories("all")}
              >
                All
              </Button>

              {storecategories.map((element, idx) => (
                <Button
                  className={id === element.id ? "" : "btn-link"}
                  color={Number(id) === Number(element.id) ? "warning" : ""}
                  key={idx}
                  size="sm"
                  onClick={() => handleCategories(element.id)}
                >
                  {element.name}
                </Button>
              ))}
            </div>
          </>
        ) : (
          <>
            <UncontrolledDropdown className="d-md-none">
              <DropdownToggle
                size="sm"
                aria-expanded={false}
                aria-haspopup={true}
                caret
                color="warning"
                data-toggle="dropdown"
                id="dropdownMenuButton"
                type="button"
              >
                {category}
              </DropdownToggle>
              <DropdownMenu aria-labelledby="dropdownMenuButton">
                <DropdownItem
                  href="#pablo"
                  onClick={() => handleBrands("all", "All")}
                >
                  All
                </DropdownItem>
                {brands.map((item, idx) => (
                  <DropdownItem
                    key={idx}
                    href="#pablo"
                    onClick={() => handleBrands(item.id, item.name)}
                  >
                    {item.name}
                  </DropdownItem>
                ))}
              </DropdownMenu>
            </UncontrolledDropdown>
            <div className="product-buttons d-none d-md-block">
              <Button
                className={brandid === "all" ? "" : "btn-link"}
                color={brandid === "all" ? "warning" : ""}
                size="sm"
                onClick={() => handleBrands("all")}
              >
                All
              </Button>

              {brands.map((element, idx) => (
                <Button
                  className={brandid === element.id ? "" : "btn-link"}
                  color={
                    Number(brandid) === Number(element.id) ? "warning" : ""
                  }
                  key={idx}
                  size="sm"
                  onClick={() => handleBrands(element.id)}
                >
                  {element.name}
                </Button>
              ))}
            </div>
          </>
        )}
      </div>
    </>
  );
}

export default ProductFilter;
