import HttpService from "./httpservice";
export const LoadNotification = () => {
  const http = new HttpService();
  let notificationUrl = "user/notifications";
  const tokenId = "user-token";
  return http
    .getData(notificationUrl, tokenId)
    .then((data) => {
      return data;
    })
    .catch((error) => {
      console.log(error);
      return error;
    });
};
export const MarkNotification = (id) => {
  const http = new HttpService();
  let notificationUrl = `user/notifications/${id}/mark`;
  const tokenId = "user-token";
  return http
    .getData(notificationUrl, tokenId)
    .then((data) => {
      return data;
    })
    .catch((error) => {
      console.log(error);
      return error;
    });
};
