export default class HttpService {
  url = `${process.env.REACT_APP_API_URL}/api`;

  postData = async (item, added_url, tokenId = "") => {
    const token = await localStorage.getItem(tokenId);
    const requestOptions = this.postRequestOptions(token, item);
    const response = await fetch(this.url + "/" + added_url, requestOptions);
    const data = await response.json();

    if (response.ok) {
      return data;
    } else {
      const error = data.message || response.status;
      return Promise.reject(error);
    }
  };

  getData = async (added_url, tokenId = "") => {
    const token = await localStorage.getItem(tokenId);
    const requestOptions = this.getRequestOptions(token);
    const response = await fetch(this.url + "/" + added_url, requestOptions);
    const data = await response.json();

    if (response.ok) {
      return data;
    } else {
      const error = data.message || response.status;
      return Promise.reject(error);
    }
  };
  putData = async (added_url, items, tokenId = "") => {
    const token = await localStorage.getItem(tokenId);
    const requestOptions = this.putRequestOptions(token, items);
    const response = await fetch(this.url + "/" + added_url, requestOptions);
    const data = await response.json();

    if (response.ok) {
      return data;
    } else {
      const error = data.message || response.status;
      return Promise.reject(error);
    }
  };

  deleteData = async (added_url, tokenId = "") => {
    const token = await localStorage.getItem(tokenId);
    const requestOptions = this.deleteRequestOptions(token);
    const response = await fetch(this.url + "/" + added_url, requestOptions);
    const data = await response.json();

    if (response.ok) {
      return data;
    } else {
      const error = data.message || response.status;
      return Promise.reject(error);
    }
  };

  getRequestOptions = (token) => {
    let requestOptions = {
      method: "GET",
      headers: {
        Authorization: "Bearer " + token,
        "Content-type": "application/json",
      },
    };
    return requestOptions;
  };
  postRequestOptions = (token, item) => {
    let requestOptions = {
      method: "POST",
      headers: {
        Authorization: "Bearer " + token,
        "Content-type": "application/json",
      },
      body: JSON.stringify(item),
    };
    return requestOptions;
  };
  putRequestOptions = (token, item) => {
    let requestOptions = {
      method: "PUT",
      headers: {
        Authorization: "Bearer " + token,
        "Content-type": "application/json",
      },
      body: JSON.stringify(item),
    };
    return requestOptions;
  };
  deleteRequestOptions = (token, item) => {
    let requestOptions = {
      method: "DELETE",
      headers: {
        Authorization: "Bearer " + token,
        "Content-type": "application/json",
      },
    };
    return requestOptions;
  };
}
