export const AddToCart = (item) => {
  const http = new HttpService();
  let cartUrl = "user/cart";
  const tokenId = "user-token";
  return http
    .postData(item, cartUrl, tokenId)
    .then((data) => {
      return data;
    })
    
};

export const UpdateCart = (id, items) => {
  const http = new HttpService();
  let cartUrl = `user/cart/${id}`;
  const tokenId = "user-token";
  return http
    .putData(cartUrl, items, tokenId)
    .then((data) => {
      return data;
    })

};

export const RemoveCart = (id) => {
  const http = new HttpService();
  let cartUrl = `user/cart/${id}}`;
  const tokenId = "user-token";
  return http
    .deleteData(cartUrl, tokenId)
    .then((data) => {
      return data;
    })
    .catch((error) => {
      console.log(error);
      return error;
    });
};

