/*eslint-disable*/
import React from "react";

// reactstrap components
import {
  Button,
  NavItem,
  NavLink,
  Nav,
  TabContent,
  TabPane,
  Input,
  FormGroup,
} from "reactstrap";
import { Link } from "react-router-dom";
import axios from "axios";
import gloNetwork from "assets/img/networks/glo.png";
import mtnNetwork from "assets/img/networks/mtn.png";
import nineMobileNetwork from "assets/img/networks/9mobile.png";
import airtelNetwork from "assets/img/networks/airtel.png";
import { toast } from "react-toastify";
import currency from "../../../js/methods/currency";
import axiosApiInstance from "../../../js/api_helper";

// core components

function AirtimeData({ confirmInfo, plainTab }) {
  const [plainTabs, setPlainTabs] = React.useState("1");
  const [network, setnetwork] = React.useState("mtn");
  const [number, setnumber] = React.useState("");
  const [amount, setamount] = React.useState("");
  const payviame_token = localStorage.getItem("payviame-token");
  const [plans, setplans] = React.useState([]);
  const [plan, setplan] = React.useState("");
  const [detail, setdetail] = React.useState("");
  const [isclicked, setisclicked] = React.useState(false);

  React.useEffect(() => {
    setnetwork("mtn");
    setamount("");
    setnumber("");
    setplan("");
    setdetail("");
  }, [plainTab, plainTabs]);
  function handleNetwork(value) {
    setnetwork(value);
  }
  function handleAmount(value) {
    setamount(value);
  }
  function handleNumber(value) {
    setnumber(value);
  }
  function handlePlan(value) {
    setplan(value);
  }
  function reset() {
    setamount("");
    setnumber("");
    setnetwork("mtn");
    setplan("");
  }
  function handlePurchase(type) {
    if (!amount) {
      toast.error("Enter an amount");
      return;
    }
    if (!number || number.length !== 11) {
      toast.error("Provide a valid phone number");
      return;
    }
    var data = {
      network,
      amount,
      number,
      plan,
      detail,
      type,
      paymentCode: plan,
    };
    setamount("");
    setnumber("");
    setnetwork("mtn");
    confirmInfo(data);
  }
  React.useEffect(() => {
    if (!number || number.length < 11) {
      return;
    }
    if (!network) {
      return;
    }
    var data = {
      mobile_number: number,
      network: network.toUpperCase(),
    };
    axiosApiInstance
      .post(`https://apis.payviame.com/api/buy-data-1`, data, {
        headers: {
          Authorization: `Bearer ${payviame_token}`,
        },
      })
      .then((res) => {
        if (res.status === 201) {
          setplans(res.data.data[0]);
        }
      });

    return () => {};
  }, [number, network, payviame_token]);

  React.useEffect(() => {
    if (!plan) return;
    var result = plans.find((item) => item.paymentCode === plan);
    if (!result) return;
    setamount(result.amount);
    setdetail(result.paymentitemname);
    return () => {};
  }, [plan]);

  return (
    <>
      <div className="airtime-data">
        <Nav className="d-flex justify-content-center" role="tablist" tabs>
          <NavItem>
            <NavLink
              className={plainTabs === "1" ? "active" : ""}
              href="#pablo"
              onClick={(e) => {
                e.preventDefault();
                setPlainTabs("1");
              }}
            >
              Airtime
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={plainTabs === "2" ? "active" : ""}
              href="#pablo"
              onClick={(e) => {
                e.preventDefault();
                setPlainTabs("2");
              }}
            >
              Data
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={"plainTabs" + plainTabs}>
          <TabPane tabId="plainTabs1">
            <div className="network-selector-block">
              <div className="network">
                <img
                  onClick={() => handleNetwork("mtn")}
                  src={mtnNetwork}
                  alt={"network"}
                />
                {network === "mtn" ? (
                  <div className="selected-state">
                    <span className="check-item">
                      <i className="fa fa-check" />
                    </span>
                  </div>
                ) : (
                  ""
                )}
              </div>
              <div className="network">
                <img
                  onClick={() => handleNetwork("glo")}
                  src={gloNetwork}
                  alt={"network"}
                />
                {network === "glo" ? (
                  <div className="selected-state">
                    <span className="check-item">
                      <i className="fa fa-check" />
                    </span>
                  </div>
                ) : (
                  ""
                )}
              </div>
              <div className="network">
                <img
                  onClick={() => handleNetwork("airtel")}
                  src={airtelNetwork}
                  alt={"network"}
                />
                {network === "airtel" ? (
                  <div className="selected-state">
                    <span className="check-item">
                      <i className="fa fa-check" />
                    </span>
                  </div>
                ) : (
                  ""
                )}
              </div>
              <div className="network">
                <img
                  onClick={() => handleNetwork("9mobile")}
                  src={nineMobileNetwork}
                  alt={"network"}
                />
                {network === "9mobile" ? (
                  <div className="selected-state">
                    <span className="check-item">
                      <i className="fa fa-check" />
                    </span>
                  </div>
                ) : (
                  ""
                )}
              </div>
            </div>
            <FormGroup>
              <label>Amount (Naira)</label>
              <Input
                placeholder="Amount"
                value={amount}
                onChange={(e) => handleAmount(e.target.value)}
                type="number"
                required
              />
            </FormGroup>
            <FormGroup>
              <label>Mobile Number</label>
              <Input
                required
                onChange={(e) => handleNumber(e.target.value)}
                placeholder="Mobile Number"
                value={number}
                type="number"
              />
            </FormGroup>
            <Button
              className="bg-primary-gradient mt-5"
              color="primary"
              size="lg"
              block
              type="button"
              onClick={() => handlePurchase("airtime")}
            >
              Purchase Airtime
            </Button>
          </TabPane>
          <TabPane tabId="plainTabs2">
            <div className="network-selector-block">
              <div className="network">
                <img
                  onClick={() => handleNetwork("mtn")}
                  src={mtnNetwork}
                  alt={"network"}
                />
                {network === "mtn" ? (
                  <div className="selected-state">
                    <span className="check-item">
                      <i className="fa fa-check" />
                    </span>
                  </div>
                ) : (
                  ""
                )}
              </div>
              <div className="network">
                <img
                  onClick={() => handleNetwork("glo")}
                  src={gloNetwork}
                  alt={"network"}
                />
                {network === "glo" ? (
                  <div className="selected-state">
                    <span className="check-item">
                      <i className="fa fa-check" />
                    </span>
                  </div>
                ) : (
                  ""
                )}
              </div>
              <div className="network">
                <img
                  onClick={() => handleNetwork("airtel")}
                  src={airtelNetwork}
                  alt={"network"}
                />
                {network === "airtel" ? (
                  <div className="selected-state">
                    <span className="check-item">
                      <i className="fa fa-check" />
                    </span>
                  </div>
                ) : (
                  ""
                )}
              </div>
              <div className="network">
                <img
                  onClick={() => handleNetwork("9mobile")}
                  src={nineMobileNetwork}
                  alt={"network"}
                />
                {network === "9mobile" ? (
                  <div className="selected-state">
                    <span className="check-item">
                      <i className="fa fa-check" />
                    </span>
                  </div>
                ) : (
                  ""
                )}
              </div>
            </div>
            <FormGroup>
              <label>Mobile Number</label>
              <Input
                required
                onChange={(e) => handleNumber(e.target.value)}
                placeholder="Mobile Number"
                value={number}
                type="number"
              />
            </FormGroup>
            <FormGroup>
              <label>Data Plan</label>
              <Input
                required
                placeholder="Select Plan"
                value={plan}
                onChange={(e) => handlePlan(e.target.value)}
                type="select"
              >
                <option disabled value="">
                  Select plan
                </option>
                {plans.length ? (
                  plans.map((item, index) => (
                    <option key={index} value={item.paymentCode}>
                      {item.paymentitemname}
                    </option>
                  ))
                ) : (
                  <option disabled value="">
                    Loading plans
                  </option>
                )}
              </Input>
            </FormGroup>

            <Button
              className="bg-primary-gradient mt-5"
              color="primary"
              size="lg"
              block
              type="button"
              onClick={() => handlePurchase("data")}
            >
              Purchase Data
            </Button>
          </TabPane>
        </TabContent>
      </div>
    </>
  );
}

export default AirtimeData;
