import React from "react";

// reactstrap components
import { Button, Input, FormGroup } from "reactstrap";

// core components

function Education({ toggle }) {
  const [amount, setamount] = React.useState("");
  const [provider, setprovider] = React.useState("");
  const [accountid, setaccountid] = React.useState("");
  function handleAmount(value) {
    setamount(value);
  }
  function handleProvider(value) {
    setprovider(value);
  }

  function handleAccountiD(value) {
    setaccountid(value);
  }
  return (
    <>
      <div className="airtime-data">
        <FormGroup>
          <label>Select Payment</label>
          <Input
            placeholder="Select Payment"
            onChange={(e) => handleProvider(e.target.value)}
            type="select"
          >
            <option value="">WAEC Scratch Card</option>
            <option>...</option>
          </Input>
        </FormGroup>
        <FormGroup>
          <label>Amount (Naira)</label>
          <Input
            placeholder="Amount"
            value={amount}
            onChange={(e) => handleAmount(e.target.value)}
            type="number"
          />
        </FormGroup>
        <FormGroup>
          <label>Mobile Number/ Account ID</label>
          <Input
            placeholder="Enter Number"
            onChange={(e) => handleAccountiD(e.target.value)}
            type="number"
          />
        </FormGroup>
        <Button
          className="bg-primary-gradient mt-5"
          color="primary"
          size="lg"
          onClick={toggle}
          block
        >
          Purchase
        </Button>
      </div>
    </>
  );
}

export default Education;
