/*eslint-disable*/
import React from "react";

// reactstrap components
import { Button, Input, FormGroup } from "reactstrap";
import axios from "axios";
import { toast } from "react-toastify";
import currency from "../../../js/methods/currency";
// core components

function Betting({ confirmInfo, plainTab }) {
  const [amount, setamount] = React.useState("");
  const [company, setcompany] = React.useState("");
  const [mobileno, setmobileno] = React.useState("");
  const payviame_token = localStorage.getItem("payviame-token");
  const [billers, setbillers] = React.useState([]);
  const [detail, setdetail] = React.useState("");
  const [user, setuser] = React.useState("");
  const [isverifying, setisverifying] = React.useState(false);
  const [isclickable, setisclickable] = React.useState(false);
    React.useEffect(() => {

      setamount("");
    

      setdetail("");
      setcompany("");
      setmobileno("");
      setuser("");

    }, [plainTab]);
  React.useEffect(() => {
    axios
      .get("https://apis.payviame.com/api/bet-billers", {
        headers: {
          Authorization: `Bearer ${payviame_token}`,
        },
      })
      .then((res) => {
        if (res.status === 200) {
          var data = res.data.data.sort((a, b) =>
            a.billername > b.billername
              ? 1
              : b.billername > a.billername
              ? -1
              : 0
          );
          setbillers(data);
        }
      });
    return () => {};
  }, [payviame_token]);
  React.useEffect(() => {
    if (!company) return;
    var result = billers.find(
      (item) => Number(item.billerid) === Number(company)
    );
    if (!result) return;

    setdetail(result.billername);
    return () => {};
  }, [company]);

  function handleAmount(value) {
    setamount(value);
  }
  function handleCompany(value) {
    setcompany(value);
  }

  function handleMobileNo(value) {
    setmobileno(value);
  }
  function verify() {
    if (!mobileno || !company || mobileno.length < 6) {
      toast.error("Fill all fields");
      return;
    }
    setisverifying(true);
    var data = {
      customerid: mobileno,
      paymentCode: company,
    };

    axios
      .post("https://apis.payviame.com/api/validate-customer", data, {
        headers: {
          Authorization: `Bearer ${payviame_token}`,
        },
      })
      .then((res) => {
        if (res.data.status === "success") {
          setisclickable(true);
          setuser(res.data.data.fullName);
          setisverifying(false);
        } else {
        }
      })
      .catch((err) => {
        setisverifying(false);
        toast.error(err.response.data.message);
      });
  }

  function handlePurchase(type) {
    if (!amount) {
      toast.error("Enter an amount");
      return;
    }
    if (!mobileno) {
      toast.error("Provide a valid phone number");
      return;
    }
    var data = {
      company,
      amount,
      mobileno,
      detail,
      user,
      type,
      customerid: mobileno,
      paymentCode: company,
    };
    confirmInfo(data);
  }
  return (
    <>
      <div className="airtime-data">
        <FormGroup>
          <label>Betting Company</label>
          <Input
            placeholder="Select Company"
            onChange={(e) => handleCompany(e.target.value)}
            type="select"
            value={company}
          >
            <option disabled value="">
              Select Company
            </option>
            {billers.length ? (
              billers.map((item) => (
                <option key={item.billerid} value={item.billerid}>
                  {item.billername}
                </option>
              ))
            ) : (
              <option disabled value="">
                Loading Company
              </option>
            )}
          </Input>
        </FormGroup>
        <FormGroup>
          <label>Amount (Naira)</label>
          <Input
            placeholder="Amount"
            value={amount}
            onChange={(e) => handleAmount(e.target.value)}
            type="number"
          />
        </FormGroup>
        <FormGroup>
          <label>Mobile Number/ Account ID</label>
          <Input
            placeholder="Enter Number"
            value={mobileno}
            onChange={(e) => handleMobileNo(e.target.value)}
            type="number"
          />
        </FormGroup>
        {isclickable ? (
          ""
        ) : (
          <Button
            className="bg-light-gradient mt-5 text-black"
            color="light"
            size="lg"
            onClick={() => verify()}
            block
            disabled={isverifying ? true : false}
          >
            Verify Detail{" "}
            {isverifying ? (
              <i className="fa fa-spinner fa-spin" aria-hidden="true"></i>
            ) : (
              ""
            )}
          </Button>
        )}
        {isclickable ? (
          <Button
            className="bg-primary-gradient mt-5"
            color="primary"
            size="lg"
            onClick={() => handlePurchase("cable")}
            block
            disabled={isclickable ? false : true}
          >
            Make payment
          </Button>
        ) : (
          ""
        )}
      </div>
    </>
  );
}

export default Betting;
