/*eslint-disable*/
import React from "react";

// reactstrap components
import { Button, Input, FormGroup } from "reactstrap";
import axios from "axios";
import currency from "../../../js/methods/currency";
import { toast } from "react-toastify";
// core components

function Internet({ confirmInfo, plainTab }) {
  const [amount, setamount] = React.useState("");
  const [provider, setprovider] = React.useState("");
  const [packagetype, setpackagetype] = React.useState("");
  const [accountid, setaccountid] = React.useState("");
  const [services, setservices] = React.useState([]);
  const payviame_token = localStorage.getItem("payviame-token");
  const [billers, setbillers] = React.useState([]);
  const [providerdetail, setproviderdetail] = React.useState("");
  const [detail, setdetail] = React.useState("");
  const [isclickable, setisclickable] = React.useState(false);
  const [user, setuser] = React.useState({});
  const [isverifying, setisverifying] = React.useState(false);


   React.useEffect(() => {
setservices("")
      setamount("");

      setdetail("");

      setuser("");
setpackagetype("");
setaccountid("")
setprovider("")
setproviderdetail("")
    }, [plainTab]);
  React.useEffect(() => {
    axios
      .get("https://apis.payviame.com/api/internet-billers", {
        headers: {
          Authorization: `Bearer ${payviame_token}`,
        },
      })
      .then((res) => {
        if (res.status === 200) {
          var data = res.data.data.sort((a, b) =>
            a.billername > b.billername
              ? 1
              : b.billername > a.billername
              ? -1
              : 0
          );
          setbillers(data);
        }
      });
    return () => {};
  }, [payviame_token]);

  function verify() {
    if (!accountid || !packagetype || accountid.length < 7) {
      toast.error("Fill all fields");
      return;
    }
    setisverifying(true);
    var data = {
      customerid: accountid,
      paymentCode: packagetype,
    };

    axios
      .post("https://apis.payviame.com/api/validate-customer", data, {
        headers: {
          Authorization: `Bearer ${payviame_token}`,
        },
      })
      .then((res) => {
        if (res.data.status === "success") {
          setisclickable(true);
          setuser(res.data.data.fullName);
          setisverifying(false);
        } else {
        }
      })
      .catch((err) => {
        setisverifying(false);
        toast.error(err.response.data.message);
      });
  }

  React.useEffect(() => {
    if (!provider) {
      return;
    }
    const biller = billers.find(
      (item) => parseInt(item.billerid) === parseInt(provider)
    );;

    setproviderdetail(
      biller.billername
    );
    axios
      .get(`https://apis.payviame.com/api/internet-items/${provider}`, {
        headers: {
          Authorization: `Bearer ${payviame_token}`,
        },
      })
      .then((res) => {
        if (res.status === 201) {
          setservices(res.data.data[0]);
        }
      });

    return () => {};
  }, [provider, payviame_token]);
  React.useEffect(() => {
    if (!packagetype) return;
    var result = services.find((item) => item.paymentCode === packagetype);
    if (!result) return;
    setamount(result.amount / 100);
    setdetail(result.paymentitemname);
    return () => {};
  }, [packagetype]);

  function handleProvider(value) {
    setprovider(value);
    setpackagetype("");
  }
  function handlePackage(value) {
    setpackagetype(value);
  }
  function handleAccountiD(value) {
    setaccountid(value);
  }
  function handlePurchase(type) {
    if (!provider) {
      toast.error("Choose a provider");
      return;
    }

    if (!packagetype) {
      toast.error("Choose a package");
      return;
    }

    var data = {
      provider,
      amount,
      accountid,
      packagetype,
      providerdetail,
      detail,
      type,
      customerid: accountid,
      paymentCode: packagetype,
    };

    confirmInfo(data);
  }

  return (
    <>
      <div className="airtime-data">
        <FormGroup>
          <label>Service Provider</label>
          <Input
            placeholder="Select Provider"
            onChange={(e) => handleProvider(e.target.value)}
            type="select"
            value={provider}
          >
            <option disabled value="">
              Select Provider
            </option>
            {billers.length ? (
              billers.map((item) => (
                <option key={item.billerid} value={item.billerid}>
                  {item.billername}
                </option>
              ))
            ) : (
              <option disabled value="">
                Loading Providers
              </option>
            )}
          </Input>
        </FormGroup>
        <FormGroup>
          <label>Package Type</label>
          <Input
            placeholder="Select Package"
            onChange={(e) => handlePackage(e.target.value)}
            type="select"
            value={packagetype}
          >
            <option disabled value="">
              Select Package
            </option>
            {services.length ? (
              services.map((item, index) => (
                <option key={index} value={item.paymentCode}>
                  {item.paymentitemname}
                </option>
              ))
            ) : (
              <option disabled value="">
                Loading Packages
              </option>
            )}
          </Input>
        </FormGroup>
        <FormGroup>
          <label>Amount (Naira)</label>
          <Input
            placeholder="Amount"
            value={currency(amount)}
            onChange={(e) => handleAmount(e.target.value)}
            type="number"
            readOnly
          />
        </FormGroup>
        <FormGroup>
          <label>Device/Account ID</label>
          <Input
            placeholder="Enter ID"
            onChange={(e) => handleAccountiD(e.target.value)}
            type="number"
            value={accountid}
          />
        </FormGroup>
        {isclickable ? (
          ""
        ) : (
          <Button
            className="bg-light-gradient mt-5 text-black"
            color="light"
            size="lg"
            onClick={() => verify()}
            block
            disabled={isverifying ? true : false}
          >
            {isverifying === true ? "Verifying " : "Verify Detail"}

            {isverifying ? (
              <i className="fa fa-spinner fa-spin" aria-hidden="true"></i>
            ) : (
              ""
            )}
          </Button>
        )}
        {isclickable ? (
          <Button
            className="bg-primary-gradient mt-5"
            color="primary"
            size="lg"
            onClick={() => handlePurchase("internet")}
            block
            disabled={isclickable ? false : true}
          >
            Purchase Internet Plan
          </Button>
        ) : (
          ""
        )}
      </div>
    </>
  );
}

export default Internet;
