import * as ActionTypes from "../constants/action-types";
const initState = {
  items: [],
  total: 0,
  commission: 0,
  shipping: 0,
  weight:0,
  location:'',
  error:null
};

const CartReducer = (state = initState, action) => {
  switch (action.type) {
    case ActionTypes.GET_CART:
      return {
        ...state,
        total: action.res.total,
        commission: action.res.commission,
        shipping: action.res.shipping,
        weight: action.res.weight,
         location: action.res.location,
        items: action.res.cart,
      };

    case ActionTypes.GET_TOTAL:
      return {
        ...state,
        total: action.res.total,
        commission: action.res.commission,
        shipping: action.res.shipping,
        weight: action.res.weight,
          location: action.res.location,
      };

    case ActionTypes.ADD_TO_CART:
      return {
        ...state,
        items: [...state.items, action.data],
      };

    case ActionTypes.UPDATE_CART:
      return {
        ...state,
        items: state.items.map((item) => {
          if (item.id === action.id) {
            item = action.res;
          }
          return item;
        }),
      };
    case ActionTypes.REMOVE_CART:
      return {
        ...state,
        items: state.items.filter((item) => item.id !== action.id),
      };

    case ActionTypes.CLEAR_CART:
      return {
        ...state,
        items: [],
      };

    case ActionTypes.CODE_ERROR:
      return {
        ...state,
        error: action.error,
      };
    case ActionTypes.RESET_ERROR:
      return {
        ...state,
        error:null,
      };
    default:
      return state;
  }
};

export default CartReducer;
