import React, { useState, useEffect } from "react";
import { useLocation, useParams } from "react-router-dom";
import axios from "axios";

function SocialAuth() {
  const baseURL = `${process.env.REACT_APP_API_URL}`;
  const location = useLocation();
  const params = useParams();
  const [code, setCode] = useState(null);
  const [scope, setScope] = useState(null);
  const [prompt, setPrompt] = useState(null);
  const [authuser, setAuthUser] = useState(null);
  const [accessToken, setAccessToken] = useState(null);
  const [clientId, setclientId] = useState(null);
  const [clientSecret, setclientSecret] = useState(null);
  const [provider, setprovider] = useState(null);

  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    setCode(queryParams.get("code"));
    setScope(queryParams.get("scope"));
    setAuthUser(queryParams.get("authuser"));
    setPrompt(queryParams.get("prompt"));
    setclientId(process.env.REACT_APP_USER_CLIENT_ID);
    setclientSecret(process.env.REACT_APP_USER_CLIENT_SECRET);
    setprovider(params.provider);
  }, [location.search, params.provider]);
  useEffect(function () {
    if (!code) return;
    if (accessToken) return;
    async function callback() {
      try {
        var data = {
          code: code,
          scope: scope,
          authuser: authuser,
          prompt: prompt,
        };

        var response = await axios.post(
          `${baseURL}/api/auth/google/callback`,
          data
        );

        var token = await response.data;

        setAccessToken(token);
      } catch (error) {}
    }
    callback();
  });
  useEffect(() => {
    function register() {
      let data = {
        grant_type: "social", // static 'social' value
        client_id: clientId,
        client_secret: clientSecret, // client secret
        provider: provider, // name of provider (e.g., 'facebook', 'google' etc.)
        access_token: accessToken, // access token issued by specified provider
      };

      axios
        .post(`${baseURL}/oauth/token`, data)
        .then((response) => {
          if (response.status === 200) {
            localStorage.setItem("user-token", response.data.access_token);
            axios
              .get(`${baseURL}/api/user`, {
                headers: {
                  Authorization: `Bearer ${response.data.access_token}`,
                },
              })
              .then((res) => {
                if (res.status === 200) {
                  localStorage.setItem("user-data", JSON.stringify(res.data));
                  axios
                    .get(`${baseURL}/api/user/payviame/token`, {
                      headers: {
                        Authorization: `Bearer ${response.data.access_token}`,
                      },
                    })
                    .then((resp) => {
                      if (resp.status === 200) {
                        localStorage.setItem("payviame-token", resp.data);
                        RefreshParent();
                      }
                    });
                }
              })
              .catch(() => {});
          }
        })
        .catch(() => {});
    }

    if (!accessToken) return;
    register();
  }, [accessToken, clientId, clientSecret, provider, baseURL]);

  function RefreshParent() {
    if (window.opener != null && !window.opener.closed) {
      window.opener.location.reload();
      window.close();
    } else {
      window.location.href = "/home";
    }
  }

  return <div>Authenticating..</div>;
}

export default SocialAuth;
