import { React, useState, useEffect } from "react";
import debounce from "lodash.debounce";
// reactstrap components
import {
  Container,
  Row,
  Col,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Pagination,
  PaginationItem,
  PaginationLink,
} from "reactstrap";

import "react-accessible-accordion/dist/fancy-example.css";

// core components
import DefaultNavbar from "components/Navbars/DefaultNavbar.js";
import DefaultFooter from "components/Footers/DefaultFooter.js";
import { Link } from "react-router-dom";
// import { useSelector } from "react-redux";
import axios from "axios";

function StoresListing() {
  const [rightFocus, setRightFocus] = useState(false);
  // const [stores, setstores] = useState([]);

  const [filteredStores, setfilteredStores] = useState([]);
  const [pagination, setpagination] = useState({});
  var [currentpage, setcurrentpage] = useState(1);
  const [pages, setpages] = useState([]);
  const [lastpage, setlastpage] = useState(null);
  const [loading, setloading] = useState(true);
  useEffect(() => {
    document.body.classList.add("landing-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("landing-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);

  useEffect(() => {
    const location = JSON.parse(localStorage.getItem("location"));
    axios
      .post(
        `${process.env.REACT_APP_API_URL}/api/get/stores?page=${currentpage}`,
        location
      )
      .then((res) => {
        if (res.status === 200) {
          //  setstores(res.data.data);
          setfilteredStores(res.data.data);
          setcurrentpage(res.data.meta.current_page);
          setlastpage(res.data.meta.last_page);
          setloading(false);
          var info = {
            current_page: res.data.meta.current_page,
            from: res.data.meta.from,
            last_page: res.data.meta.last_page,
            per_page: res.data.meta.per_page,
            to: res.data.meta.to,
            total: res.data.meta.total,
          };
          setpagination(info);

          if (res.data.meta.total) {
            var pages = [];
            for (
              let i = 1;
              i <= Math.ceil(res.data.meta.total / res.data.meta.per_page);
              i++
            ) {
              pages.push(i);
            }

            setpages(pages);
          }
        }
      });
  }, [currentpage]);

  function handlecurrentpage(val) {
    setcurrentpage(val);
  }
  function handleNext() {
    if (currentpage === lastpage) return;
    var current = currentpage;
    current++;
    setcurrentpage(current);
  }

  function handlePrev() {
    if (currentpage === 1) return;
    var current = currentpage;
    current--;
    setcurrentpage(current);
  }

  function handleSearch(e) {
    var value = e.target.value;

    const location = JSON.parse(localStorage.getItem("location"));
    var data = {
      query: value,
      lat: location.lat,
      long: location.long,
    };

    const debouncedSearch = debounce(() => {
      axios
        .post(`${process.env.REACT_APP_API_URL}/api/search/stores`, data)
        .then((res) => {
          if (res.status === 200) {
            // setstores(res.data.data);
            setfilteredStores(res.data.data);
            setcurrentpage(res.data.meta.current_page);
            setlastpage(res.data.meta.last_page);
            setloading(false);
            var info = {
              current_page: res.data.meta.current_page,
              from: res.data.meta.from,
              last_page: res.data.meta.last_page,
              per_page: res.data.meta.per_page,
              to: res.data.meta.to,
              total: res.data.meta.total,
            };
            setpagination(info);

            if (res.data.meta.total) {
              var pages = [];
              for (
                let i = 1;
                i <= Math.ceil(res.data.meta.total / res.data.meta.per_page);
                i++
              ) {
                pages.push(i);
              }

              setpages(pages);
            }
          }
        });
    }, 1500);
    debouncedSearch();
  }

  let renderedpages = pages.map((page, index) => (
    <PaginationItem key={index} active={currentpage === page}>
      <PaginationLink onClick={() => handlecurrentpage(page)}>
        {page}
      </PaginationLink>
    </PaginationItem>
  ));

  return (
    <>
      <DefaultNavbar />
      <div className="wrapper">
        {!loading ? (
          <section className="section">
            <Container>
              <Row>
                <Col className="ml-auto mr-auto text-center" lg="6">
                  <div className="mb-5">
                    <h4 className="font-weight-bold">Search here</h4>
                  </div>
                  <InputGroup className={rightFocus ? "input-group-focus" : ""}>
                    <Input
                      placeholder="Search for store names"
                      type="search"
                      onChange={(e) => handleSearch(e)}
                      onFocus={() => setRightFocus(true)}
                      onBlur={() => setRightFocus(false)}
                    />
                    <InputGroupAddon addonType="append">
                      <InputGroupText>
                        <i className="fa fa-search" />
                      </InputGroupText>
                    </InputGroupAddon>
                  </InputGroup>
                </Col>
              </Row>
              <Row>
                <Col className="ml-auto mr-auto text-center" lg="6">
                  <h4 className="font-weight-bold">Showing all stores</h4>
                </Col>
              </Row>
              <Row>
                {filteredStores.length ? (
                  filteredStores.map((item, index) => (
                    <Col
                      className="stores"
                      lg="4"
                      md="6"
                      xs="6"
                      key={index}
                    >
                      <Link
                        to={`/product-list/${item.id}`}
                        className="d-block"
                      >
                        <img alt="..." className="store-img" src={item.image} />
                        <h5 className="store-name">{item.name}</h5>
                        <div className="d-flex justify-content-between store-address">
                          <p>
                            <i className="fa fa-map-marker-alt mr-2" />
                            {item.location}
                          </p>
                          <span className="text-primary">
                            {item.distance} mi
                          </span>
                        </div>
                      </Link>
                    </Col>
                  ))
                ) : (
                  <Col className=" d-flex justify-content-center p-3 txt-center">
                    {" "}
                    No Store available
                  </Col>
                )}
              </Row>
              {filteredStores.length === 20 ? (
                <Row>
                  <Col className="d-flex justify-content-center mt-4">
                    <Pagination aria-label=" navigation ">
                      <PaginationItem>
                        <PaginationLink
                          first
                          onClick={() => handlecurrentpage(1)}
                        />
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink onClick={() => handlePrev()} previous />
                      </PaginationItem>
                      {renderedpages}
                      <PaginationItem>
                        <PaginationLink onClick={() => handleNext()} next />
                      </PaginationItem>
                      <PaginationItem>
                        <PaginationLink
                          onClick={() =>
                            handlecurrentpage(pagination.last_page)
                          }
                          last
                        />
                      </PaginationItem>
                    </Pagination>
                  </Col>
                </Row>
              ) : (
                ""
              )}
            </Container>
          </section>
        ) : (
          <section
            style={{ height: "80vh" }}
            className="p-5 d-flex justify-content-center align-items-center"
          >
            <span className="text-center">
              <i
                className="fa fa-spinner fa-spin fa-4x text-primary"
                aria-hidden="true"
              ></i>
              <br /> <br />
              <span>Loading nearest stores...</span>
            </span>
          </section>
        )}

        <DefaultFooter />
      </div>
    </>
  );
}

export default StoresListing;
