import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import { Provider } from "react-redux";
import store from "./js/store/index";
import { ToastContainer } from "react-toastify";

// styles for this kit
import "assets/css/bootstrap.min.css";
import "assets/scss/now-ui-kit.scss?v=1.5.0";
import "assets/demo/demo.css?v=1.5.0";
import "assets/css/gtwalsheimpro.css";
import "assets/demo/nucleo-icons-page-styles.css?v=1.5.0";
import "react-toastify/dist/ReactToastify.css";

// pages for this kit
import Index from "views/Index.js";
import NucleoIcons from "views/NucleoIcons.js";
import LoginPage from "views/auth/LoginPage.js";
import SignUpPage from "views/auth/SignUpPage";
import SocialAuth from "views/auth/SocialAuth.js";
import ForgotPasswordPage from "views/auth/ForgotPasswordPage";
import ResetPasswordPage from "views/auth/ResetPasswordPage";
import LandingPage from "views/examples/LandingPage.js";
import ProfilePage from "views/examples/ProfilePage.js";
import HomePage from "views/HomePage";
import AboutPage from "views/AboutPage";
import Faqs from "views/Faqs";
import TermsOfService from "views/TermsOfService";
import Blog from "views/Blog";
import StoresListing from "views/StoresListing";
import StoreDetails from "views/StoreDetails";
import ProductDetails from "views/ProductDetails";
import CheckOut from "views/CheckOut";
import OrderReceipt from "views/OrderReceipt";
import Profile from "views/Profile";
import Notification from "./views/Notification";

ReactDOM.render(
  <Provider store={store}>
    <ToastContainer autoClose={1500} />
    <BrowserRouter>
      <Switch>
        <Route
          exact
          path="/"
          render={(props) => <Redirect to={{ pathname: "/home" }} />}
        />
        <Route exact path="/home" render={(props) => <Index {...props} />} />

        <Route
          exact
          path="/nucleo-icons"
          render={(props) => <NucleoIcons {...props} />}
        />
        <Route
          exact
          path="/home-page"
          render={(props) => <HomePage {...props} />}
        />
        <Route
          exact
          path="/about-us"
          render={(props) => <AboutPage {...props} />}
        />
        <Route exact path="/faqs" render={(props) => <Faqs {...props} />} />
        <Route
          exact
          path="/terms-of-service"
          render={(props) => <TermsOfService {...props} />}
        />
        <Route exact path="/blog" render={(props) => <Blog {...props} />} />
        <Route
          exact
          path="/landing-page"
          render={(props) => <LandingPage {...props} />}
        />
        <Route
          exact
          path="/stores"
          render={(props) => <StoresListing {...props} />}
        />
        <Route
          exact
          path="/product-list/:store_id"
          render={(props) => <StoreDetails {...props} />}
        />
        <Route
          exact
          path="/product-details/:product_id"
          render={(props) => <ProductDetails {...props} />}
        />
        <Route
          exact
          path="/checkout"
          render={(props) => <CheckOut {...props} />}
        />
        <Route
          exact
          path="/order-receipt"
          render={(props) => <OrderReceipt {...props} />}
        />
        <Route
          exact
          path="/profile-page"
          render={(props) => <ProfilePage {...props} />}
        />
        <Route
          exact
          path="/profile"
          render={(props) => <Profile {...props} />}
        />
        <Route
          exact
          path="/login"
          render={(props) => <LoginPage {...props} />}
        />
        <Route
          exact
          path="/sign-up"
          render={(props) => <SignUpPage {...props} />}
        />
        <Route
          exact
          path="/notifications"
          render={(props) => <Notification {...props} />}
        />
        <Route
          exact
          path="/forgot-password"
          render={(props) => <ForgotPasswordPage {...props} />}
        />
        <Route
          exact
          path="/reset-password"
          render={(props) => <ResetPasswordPage {...props} />}
        />
        <Route
          exact
          path="/auth/:provider/callback"
          render={(props) => <SocialAuth {...props} />}
        />
        <Redirect to="/home" />
        <Redirect from="/" to="/home" />
      </Switch>
    </BrowserRouter>
  </Provider>,
  document.getElementById("root")
);
