import React from "react";

// reactstrap components
import {
  Container,
  NavItem,
  NavLink,
  Nav,
  TabContent,
  TabPane,
} from "reactstrap";
import { Link, useLocation, useHistory } from "react-router-dom";
// core components
import DefaultNavbar from "components/Navbars/DefaultNavbar.js";
import DefaultFooter from "components/Footers/DefaultFooter.js";
import AccountInfo from "../components/ProfileComponents/AccountInfo/AccountInfo";
import Wallet from "../components/ProfileComponents/Wallet/Wallet";
import Security from "../components/ProfileComponents/Security/Security";
import Orders from "../components/ProfileComponents/Orders/Orders";
import ShoppingList from "../components/ProfileComponents/ShoppingList/ShoppingList";

function Profile() {
  const [iconTabs, setIconTabs] = React.useState("1");
  const location = useLocation();
  const token = localStorage.getItem("user-token");
  const history = useHistory();
  React.useEffect(() => {
    if (!token) {
      history.push("/login");
    }
    return () => {};
  }, [history, token]);

  React.useEffect(() => {
    document.body.classList.add("profile-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("profile-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);

  React.useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    if (queryParams.get("showing")) {
      setIconTabs(queryParams.get("showing"));
    }

    return () => {};
  }, [location.search]);

  return (
    <>
      <DefaultNavbar />
      <div className="wrapper">
        <section className="section section-profile">
          <Container>
            <div className="d-lg-flex d-md-flex d-sm-block d-block">
              <div className="nav-block d-none d-md-block">
                <Nav className="flex-column" role="tablist" tabs>
                  <NavItem>
                    <NavLink
                      className={iconTabs === "1" ? "active" : ""}
                      href="#pablo"
                      onClick={(e) => {
                        e.preventDefault();
                        setIconTabs("1");
                      }}
                    >
                      <i className="fa fa-pen mr-3" />
                      Account Information
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={iconTabs === "2" ? "active" : ""}
                      href="#pablo"
                      onClick={(e) => {
                        e.preventDefault();
                        setIconTabs("2");
                      }}
                    >
                      <i className="far fa-credit-card mr-3" />
                      Wallet
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={iconTabs === "3" ? "active" : ""}
                      href="#pablo"
                      onClick={(e) => {
                        e.preventDefault();
                        setIconTabs("3");
                      }}
                    >
                      <i className="fa fa-list-ul mr-3" />
                      My Shopping list
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={iconTabs === "4" ? "active" : ""}
                      href="#pablo"
                      onClick={(e) => {
                        e.preventDefault();
                        setIconTabs("4");
                      }}
                    >
                      <i className="far fa-list-alt mr-3" />
                      My Orders
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={iconTabs === "5" ? "active" : ""}
                      href="#pablo"
                      onClick={(e) => {
                        e.preventDefault();
                        setIconTabs("5");
                      }}
                    >
                      <i className="fa fa-key mr-3" />
                      Security
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={iconTabs === "6" ? "active" : ""}
                      to="/faqs"
                      tag={Link}
                    >
                      <i className="fa fa-graduation-cap mr-3" />
                      FAQ
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={iconTabs === "7" ? "active" : ""}
                      href="#pablo"
                      onClick={(e) => {
                        e.preventDefault();
                        setIconTabs("7");
                      }}
                    >
                      <i className="fa fa-headset mr-3" />
                      Support
                    </NavLink>
                  </NavItem>
                </Nav>
              </div>
              <div className="tabs-block">
                <TabContent className="" activeTab={"iconTabs" + iconTabs}>
                  <TabPane tabId="iconTabs1">
                    <AccountInfo />
                  </TabPane>
                  <TabPane tabId="iconTabs2">
                    <Wallet />
                  </TabPane>
                  <TabPane tabId="iconTabs3">
                    <ShoppingList />
                  </TabPane>
                  <TabPane tabId="iconTabs4">
                    <Orders />
                  </TabPane>
                  <TabPane tabId="iconTabs5">
                    <Security />
                  </TabPane>
                  <TabPane tabId="iconTabs6" />
                  <TabPane tabId="iconTabs7">
                    <p>Support</p>
                  </TabPane>
                </TabContent>
              </div>
            </div>
          </Container>
        </section>

        <DefaultFooter />
      </div>
    </>
  );
}

export default Profile;
