import {
  LoadNotification,
  MarkNotification,
} from "../services/notificationservice";
import * as ActionTypes from "../constants/action-types";
export const LoadNotificationAction = () => {
  return (dispatch) => {

    LoadNotification().then(
      (res) => {
        dispatch({ type: ActionTypes.LOAD_NOTIFICATION_SUCCESS, res });
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};

export const MarkNotificationAction = (id) => {
  return (dispatch) => {

    MarkNotification(id).then(
      (res) => {
        dispatch({ type: ActionTypes.LOAD_NOTIFICATION_SUCCESS, res });
      },
      (error) => {
        dispatch({ type: ActionTypes.CODE_ERROR, error });
      }
    );
  };
};
