import React from "react";

// reactstrap components
import {
  Container,
  Row,
  Col,
} from "reactstrap";

// core components
import DefaultNavbar from "components/Navbars/DefaultNavbar.js";
import AboutPageHeader from "components/Headers/AboutPageHeader.js";
import Partners from "../components/Partners/Partners";
import GetTheApp from "../components/GetTheApp/GetTheApp";
import Subscribe from "../components/Subscribe/Subscribe";
import DefaultFooter from "components/Footers/DefaultFooter.js";

function AboutPage() {
  React.useEffect(() => {
    document.body.classList.add("landing-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("landing-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);
  return (
    <>
      <DefaultNavbar />
      <div className="wrapper">
        <AboutPageHeader />
        <section className="section-management">
          <Container>
            <Row className="justify-content-center text-center">
              <Col lg={{size: 4}}>
                <div className="team-container bg-primary">
                  <img
                      alt="..."
                      className="team-image rounded-circle"
                      src={require("assets/img/demo/team-1.png").default}
                  />

                </div>
                <h5 className="title">John Doe</h5>
                <p>Co-Founder / CTO</p>
              </Col>
              <Col lg={{size: 4}}>
                <div className="team-container bg-danger">
                  <img
                      alt="..."
                      className="team-image rounded-circle"
                      src={require("assets/img/demo/team-2.png").default}
                  />

                </div>
                <h5 className="title">John</h5>
                <p>Co-Founder / COO</p>
              </Col>

            </Row>
          </Container>
        </section>

        <Partners/>

        <section className="section section-feedback">
          <Container>
            <Row>
              <Col className="ml-auto mr-auto text-center" md="10">
                <h2 className="title text-primary">Feedback from our users</h2>
              </Col>
            </Row>
            <Row>
              <Col></Col>
            </Row>
          </Container>
        </section>

        <GetTheApp/>

        <Subscribe/>

        <DefaultFooter />
      </div>
    </>
  );
}

export default AboutPage;
