import HttpService from "./httpservice";
export const LoadProfile = () => {
  const http = new HttpService();
  let profileUrl = "user/profile";
  const tokenId = "user-token";
  return http
    .getData(profileUrl, tokenId)
    .then((data) => {
      return data;
    })
    .catch((error) => {
      console.log(error);
      return error;
    });
};
