import React from "react";
import { AddCartAction } from "../js/actions/cartaction";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { useParams, Link } from "react-router-dom";
import currency from "../js/methods/currency";
import Carousel from "react-gallery-carousel";
import "react-gallery-carousel/dist/index.css";
// reactstrap components
import {
  Container,
  Row,
  Col,
  Breadcrumb,
  BreadcrumbItem,
  Button,
  Modal,
  ModalBody,
  FormGroup,
  Input,
  Label,
} from "reactstrap";

// core components
import DefaultNavbar from "components/Navbars/DefaultNavbar.js";
import DefaultFooter from "components/Footers/DefaultFooter.js";
import SimilarProducts from "../components/SimilarProducts/SimilarProducts";
import { toast } from "react-toastify";
import red from "../assets/img/illustrations/quarter-circle.png";
import yellow from "../assets/img/illustrations/quarter-yellow.png";

function StoreDetails() {
  const dispatch = useDispatch();
  const params = useParams();
  const cart = useSelector((state) => state.userCart).items;
  const [product, setproduct] = React.useState(null);
  const [wishlist, setwishlist] = React.useState([]);
  const token = localStorage.getItem("user-token");
  const [modal, setModal] = React.useState(false);
  const toggle = () => setModal(!modal);
  const [switchList, setswitchlist] = React.useState("1");
  const [similarItems, setsimilarItems] = React.useState([]);
  const [listname, setlistname] = React.useState("");
  const [wishlistId, setwishlistId] = React.useState(null);
  const [updateCart, setupdateCart] = React.useState(false);
  const [mycart, setmycart] = React.useState([false]);



  React.useEffect(() => {
    if (!localStorage.getItem("user-token")) {
      setmycart(JSON.parse(localStorage.getItem("cartItems")));
    } else {
      localStorage.setItem("cartItems", JSON.stringify(cart));
      setmycart(cart);
    }
  }, [cart]);

  React.useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_API_URL}/api/products/${params.product_id}`)
      .then((res) => {
        if (res.status === 200) {
          setproduct(res.data);
        }
      });
    axios
      .get(
        `${process.env.REACT_APP_API_URL}/api/similar/products/${params.product_id}`
      )
      .then((res) => {
        if (res.status === 200) {
          setsimilarItems(res.data);
        }
      });
    if (token) {
      axios
        .get(`${process.env.REACT_APP_API_URL}/api/wishlists`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then((res) => {
          if (res.status === 200) {
            setwishlist(res.data);
          }
        });
    }
  }, [params.product_id, token]);

  function addToCart(product) {
    if (!token) {
      addtolocalstorage(product);
      return;
    }
    var data = {
      product_id: product.id,
      store_id: product.store.id,
    };
    dispatch(AddCartAction(data));
  }
  function isInCart(product_id, store_id) {
    return mycart.some(
      (item) =>
        Number(item.product_id) === Number(product_id) &&
        Number(item.store_id) === Number(store_id)
    );
  }

  // function addToList(product_id) {
  //   var data = {
  //     product_id,
  //   };
  //   axios
  //     .post(`${process.env.REACT_APP_API_URL}/api/wishlists`, data, {
  //       headers: {
  //         Authorization: `Bearer ${token}`,
  //       },
  //     })
  //     .then((res) => {
  //       if (res.status === 201) {
  //         var newlist = [];
  //         newlist.push(res.data);
  //         setwishlist(newlist);
  //         toast.success("Added to your wishlist");
  //       }
  //     });
  // }
  function addtolocalstorage(product) {
    let product_id = [],
      cartArray = [];
    product.product_id = product.id;
    product.store_id = product.store.id;
    product.store_name = product.store.name;
    product.description = product.product_desc;
    product.image = product.image[0];
    if (localStorage.getItem("cartItems")) {
      cartArray = JSON.parse(localStorage.getItem("cartItems"));
      if(cartArray.length){
         let storeId = cartArray[0].store_id;
         if (storeId !== product.store.id) {
           toast.info("Sorry, you can only shop one store at a time!");
           return;
         }
      }
      product_id = cartArray.map((item) => item.id);
      if (!product_id.includes(product.id)) {
        product.quantity = 1;
        cartArray.push(product);
      } else {
        cartArray.map((val) => {
          if (val.id === product.id) {
            val.quantity = val.quantity + 1;
          }
          return val;
        });
      }
    } else {
      product.quantity = 1;
      cartArray.push(product);
    }

    localStorage.setItem("cartItems", JSON.stringify(cartArray));
    toast.success("Added to cart");

    setupdateCart(!updateCart);
  }
  function isInList(product_id, store_id) {
    if (!wishlist.length) return false;
    return wishlist.some(
      (item) =>
        Number(item.product_id) === Number(product_id) &&
        Number(item.store_id) === Number(store_id)
    );
  }
  React.useEffect(() => {
    document.body.classList.add("landing-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    return function cleanup() {
      document.body.classList.remove("landing-page");
      document.body.classList.remove("sidebar-collapse");
    };
  }, []);

  function handleListSwitch(val) {
    setswitchlist(val);
  }
  function handleListSubmit() {
    if (!token) {
      toast.info("Login to complete action");
      return;
    }
    axios
      .post(
        `${process.env.REACT_APP_API_URL}/api/wishlists`,
        { name: listname },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        if (res.status === 200) {
          setwishlist(res.data);
          setswitchlist("1");
        }
      });
  }
  function handleListName(e) {
    setlistname(e.target.value);
  }
  function handleWishlistSelect(id) {
    setwishlistId(id);
  }
  function handleListAdd() {
    if (!token) {
      toast.info("Login to complete action");
      return;
    }
    axios
      .post(
        `${process.env.REACT_APP_API_URL}/api/wishlist/item`,
        { wishlist_id: wishlistId, product_id: params.product_id },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        if (res.status === 200) {
          setwishlist(res.data);
          toggle();
          toast.success("Added to list");
        }
      });
  }
  return (
    <>
      <DefaultNavbar updateCart={updateCart} />
      <div className="wrapper">
        {product ? (
          <section className="section section-product-details">
            <Container>
              <div className="d-flex align-items-center mt-4 mb-2">
                <div className="mt-4">
                  {" "}
                  <Button className="btn-link my-0" size="lg">
                    <Link to={`/product-list/${product.store.id}`}>
                      <i className="fa fa-arrow-left text-black-50" />{" "}
                    </Link>
                  </Button>{" "}
                </div>
                <Breadcrumb className="custom-breadcrumb">
                  <BreadcrumbItem>
                    <Link to={`/product-list/${product.store.id}`}>
                      {product.store.name}
                    </Link>
                  </BreadcrumbItem>
                  <BreadcrumbItem>
                    <Link
                      to={`/product-list/${product.store.id}?category=${product.category.id}`}
                    >
                      {product.category.name}
                    </Link>
                  </BreadcrumbItem>
                  <BreadcrumbItem active>{product.product_name}</BreadcrumbItem>
                </Breadcrumb>
              </div>
            </Container>
            <Container>
              <Row>
                <Col className="" lg="8">
                  <Row>
                    <Col>
                      <div className="promo-block">
                        Current Promo: Buy 2 pcs and get 1 free. Offer till next
                        stock.
                      </div>
                    </Col>
                  </Row>
                  <Row className="mt-4 product-details">
                    <Col lg="5">
                      <div className="product-image-block">
                        {product.image && product.image.length ? (
                          <Carousel
                            images={product.image.map((item) => ({
                              src: item,
                            }))}
                            hasMediaButton={false}
                            hasLeftButton={false}
                            hasIndexBoard={false}
                            hasRightButton={false}
                            hasSizeButton={false}
                            style={{
                              height: 250,
                              background: "transparent",
                            }}
                          />
                        ) : (
                          ""
                        )}
                      </div>
                      {product.in_stock > 0 && (
                        <p>{product.in_stock} Items Available</p>
                      )}
                    </Col>
                    <Col lg="7">
                      <div className="">
                        <div className="d-flex justify-content-end justify-content-md-start">
                          <p className="product-location">
                            <i className="fa fa-map-marker-alt mr-2" />
                            {product.store.name}, {product.store.location}
                          </p>
                        </div>
                        <h4 className="product-name">{product.product_name}</h4>
                        <p className="product-description">
                          {product.product_desc}
                        </p>
                        <div className="more-details">
                          {product.manufacture_date ? (
                            <div className="mb-2">
                              <span className="col-one">
                                Manufacturing Date:
                              </span>
                              <span className="col-two">
                                {" "}
                                {product.manufacture_date}
                              </span>
                            </div>
                          ) : (
                            ""
                          )}

                          {product.expiry_date ? (
                            <div className="mb-2">
                              <span className="col-one">Expiry Date:</span>
                              <span className="col-two">
                                {" "}
                                {product.expiry_date}
                              </span>
                            </div>
                          ) : (
                            ""
                          )}

                          <div className="mb-2">
                            <span className="col-one">Product No:</span>
                            <span className="col-two">
                              {product.product_no}
                            </span>
                          </div>
                          {product.batch_no ? (
                            <div className="mb-2">
                              <span className="col-one">Batch No:</span>
                              <span className="col-two">
                                {" "}
                                {product.batch_no}
                              </span>
                            </div>
                          ) : (
                            ""
                          )}
                        </div>
                      </div>
                    </Col>
                    <Col className="d-flex d-lg-none justify-content-center align-items-center">
                      <span className="display-5 font-weight-bold text-center">
                        {product.sales_price ? (
                          <>
                            <span className="miniprice">
                              {currency(product.price)}
                            </span>
                            <br />
                          </>
                        ) : (
                          ""
                        )}
                        {currency(
                          product.sales_price
                            ? product.sales_price
                            : product.price
                        )}
                      </span>
                    </Col>
                  </Row>
                  <Row>
                    <Col lg="8" md="6" sm="12">
                      {product.in_stock > 0 ? (
                        <>
                          {!isInCart(product.id, product.store.id) ? (
                            <Button
                              color="primary"
                              className="bg-primary-gradient"
                              block
                              size="lg"
                              onClick={() => addToCart(product)}
                            >
                              Add Item to Cart
                            </Button>
                          ) : (
                            <Button
                              color="secondary"
                              className="bg-primary-gradient"
                              block
                              size="lg"
                            >
                              Added to Cart
                            </Button>
                          )}
                        </>
                      ) : (
                        <div className="d-flex align-items-center justify-content-center h-100"><i className="fa fa-exclamation-triangle mr-2 text-warning" aria-hidden="true"></i> This product is out of stock</div>
                      )}
                    </Col>
                    <Col lg="4" md="6" sm="12">
                      {isInList(product.id, product.store.id)}
                      <Button
                        color="primary"
                        block
                        outline
                        size="lg"
                        onClick={toggle}
                        // onClick={() => addToList(product.id)}
                        // disabled={
                        //   isInList(product.id, product.store.id) ? true : false
                        // }
                      >
                        Add Item to List
                      </Button>
                    </Col>
                  </Row>
                </Col>

                <Col className="d-none d-lg-flex justify-content-start align-items-center">
                  <div className="display-5 font-weight-bold">
                    {product.sales_price ? (
                      <>
                        <span className="miniprice">
                          {currency(product.price)}
                        </span>
                        <br />
                      </>
                    ) : (
                      ""
                    )}
                    {currency(
                      product.sales_price ? product.sales_price : product.price
                    )}
                  </div>
                </Col>
              </Row>
              {similarItems.length ? (
                <SimilarProducts similarItems={similarItems} />
              ) : (
                ""
              )}
            </Container>
          </section>
        ) : (
          <section
            style={{ height: "80vh" }}
            className="p-5 d-flex justify-content-center align-items-center"
          >
            <i
              className="fa fa-spinner fa-spin fa-6x text-primary"
              aria-hidden="true"
            ></i>
          </section>
        )}
        <DefaultFooter />
      </div>
      <Modal
        isOpen={modal}
        toggle={toggle}
        centered
        className="section-product-details-modal"
      >
        <ModalBody className="p-0 position-relative">
          {switchList === "1" ? (
            <div className="p-3 p-md-5 lists-body">
              <h5 className="mb-3 text-center text-capitalize">
                Add item to list{" "}
              </h5>
              <div className="mb-4 lists">
                {wishlist.length ? (
                  wishlist.map((item) => (
                    <div
                      key={item.id}
                      className="d-flex justify-content-between p-3 bg-light rounded mb-2 list_item"
                      onClick={() => handleWishlistSelect(item.id)}
                    >
                      <span>{item.name}</span>
                      <span>
                        {" "}
                        <span>{item.wishlistitems.length} items</span>{" "}
                        <span>
                          {" "}
                          {wishlistId === item.id ? (
                            <i
                              className="fas fa-dot-circle text-primary"
                              aria-hidden="true"
                            ></i>
                          ) : (
                            <i
                              className="far fa-dot-circle text-muted"
                              aria-hidden="true"
                            ></i>
                          )}
                        </span>
                      </span>
                    </div>
                  ))
                ) : (
                  <div className="text-center">No list created yet</div>
                )}

                <div className="text-right">
                  <Button
                    className="btn-link text-primary"
                    onClick={() => handleListSwitch("2")}
                  >
                    Add new list
                  </Button>
                </div>
              </div>
              {wishlist.length ? (
                <div className="d-flex justify-content-between ">
                  <Button
                    color="danger"
                    onClick={toggle}
                    className="px-md-4"
                    outline
                  >
                    Cancel
                  </Button>
                  <Button
                    color="primary"
                    onClick={() => handleListAdd()}
                    className="px-md-5 bg-success-gradient"
                  >
                    Save
                  </Button>
                </div>
              ) : (
                ""
              )}
            </div>
          ) : (
            ""
          )}

          {switchList === "2" ? (
            <div className="p-3 p-md-5 list-body">
              <h5 className="mb-3 text-center text-capitalize">
                Add new list{" "}
              </h5>
              <div className="mb-4 lists">
                <FormGroup>
                  <Label for="exampleEmail">Enter list name</Label>
                  <Input
                    type="text"
                    name="text"
                    id="text"
                    placeholder="Shopping list"
                    value={listname}
                    onChange={(e) => handleListName(e)}
                  />
                </FormGroup>
              </div>
              <div className="d-flex justify-content-between ">
                <Button
                  color="danger"
                  onClick={toggle}
                  className="px-md-4"
                  outline
                >
                  Cancel
                </Button>
                <Button
                  color="primary"
                  className="px-md-5 bg-primary-gradient"
                  onClick={handleListSubmit}
                >
                  Save
                </Button>
              </div>
            </div>
          ) : (
            ""
          )}
          {switchList === "3" ? (
            <div className="p-3 p-md-5 list-body d-flex justify-content-center align-items-center ">
              <div className="text-center">
                <i
                  className="fas fa-check-circle fa-5x text-success"
                  aria-hidden="true"
                ></i>{" "}
                <br />
                <span className="font-weight-bold">
                  List added successfully
                </span>
              </div>
            </div>
          ) : (
            ""
          )}

          {switchList === "4" ? (
            <div className="p-3 p-md-5 list-body d-flex justify-content-center align-items-center ">
              <div className="text-center">
                <i
                  className="fas fa-check-circle fa-5x text-success"
                  aria-hidden="true"
                ></i>{" "}
                <br />
                <span className="font-weight-bold">Saved to shopping list</span>
              </div>
            </div>
          ) : (
            ""
          )}

          <img alt="red" src={red} className="modal-red" />
          <img alt="yellow" src={yellow} className="modal-yellow" />
        </ModalBody>
      </Modal>
    </>
  );
}

export default StoreDetails;
